/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base
@import HubStyleTokens/stylesheets/tokens

$whitespace-between-buttons-and-tooltips: 16px // #2787
$ui-moveable-area-padding: 10px
$ui-floating-button-group-height: 32px
$floating-button-group-within-children: 8px

.private-floating-button__group
  position: relative

.private-floating-button-group__buttons
  margin-top: -$whitespace-between-buttons-and-tooltips
  padding-top: $whitespace-between-buttons-and-tooltips
  position: absolute
  right: $ui-moveable-area-padding
  top: -$ui-floating-button-group-height + $floating-button-group-within-children
  z-index: 1

  .private-file-button__input
    display: none !important
    visibility: hidden !important

  > *
    +set-font-shorthand($button-sm-font-size)
    +input-metrics($button-sm-padding-y, $button-sm-padding-x, $responsive: false)

    background-color: $floating-button-group-item-background-color !important
    border-color: $floating-button-group-item-border-color !important
    color: $floating-button-group-item-color !important
    width: auto

    &:not([aria-disabled='true'])
      // sass-lint:disable nesting-depth
      &:hover,
      &.private-button--hover
        background-color: $floating-button-group-item-hover-background-color !important
        color: $floating-button-group-item-color !important
      // sass-lint:enable nesting-depth

      &.private-button--active
        background-color: $floating-button-group-item-selected-background-color !important
        color: $floating-button-group-item-color !important

.private-floating-button-group__buttons--standalone
  left: 0
  right: 0
  top: 0
