/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

$webkit-header-title-alignment-tweak: 2px

.private-header

  /// prescribed spacing and alignment for badge within header title
  .private-header__badge
    margin-left: rem-calc(8px)
    vertical-align: middle

/// always remove bottom margin within `.private-template`, same as `--condensed`
.private-template .private-template__section > .private-header
  +m-bottom-0
  border-bottom: 0

/// always remove header L & R padding w/in `.private-template`, same as `--flush`
.private-template:not(.private-editor) > .private-template__section--header > .private-header,
.private-header--flush
  padding-left: 0
  padding-right: 0

  &::after
    left: 0
    width: 100%

.private-header__title
  max-width: 100%

.private-header .private-header__title .private-header__heading
  margin-bottom: 0
  max-width: 100%
  padding-right: rem-calc(28px)

  &.private-header__heading--solo
    padding-right: 0

  // Tweak visual alignment in Chrome and Safari (#4950)
  @media screen and (-webkit-min-device-pixel-ratio: 0)
    margin-bottom: rem-calc($webkit-header-title-alignment-tweak)

    // resets ^ for Edge, which also supports -webkit-min-device-pixel-ratio
    @supports (-ms-ime-align: auto)
      margin-bottom: 0

.private-header__title-badge
  +flex-shrink(0)
  margin-right: rem-calc($base-spacing-x)
  white-space: nowrap

// When we're adequately wide, keep the title and toolbar on one line (#2593)
.private-header:not(.private-header--width-constrained)
  .private-header__title
    +display(flex)
    +flex-basis(0)
    +flex-grow(1)
    +align-items(center)

  .private-header__title + .private-header__toolbar
    +flex-grow(0)
