/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

// UIPanel

.private-panel
  +display(inline-flex)
  +flex-direction(row)
  background: $modal-background-color
  height: 100%
  max-width: 100vw
  min-width: 300px
  overflow-x: hidden
  overflow-y: auto
  z-index: $jupiter-layer

  &.private-panel--sandboxed
    bottom: 0
    position: absolute
    top: 0

  &.private-panel--right
    box-shadow: -3px 0 8px 0 $panel-shadow-color
    right: 0

  &.private-panel--left
    box-shadow: 3px 0 8px 0 $panel-shadow-color
    left: 0

.private-panel__container
  +display(flex)
  +flex-direction(column)
  min-height: 100%
  padding-top: rem-calc($modal-body-padding-y)
  width: 100%

  // Convoluted selector to match the close button in the (deprecated) headerless panel pattern (see #3279)
  &:not(.private-panel__container--with-header)
    .private-close__button
      color: $modal-close-button-foreground-color

  .private-panel__header
    .private-close__button
      color: $panel-close-button-foreground-color

  > .private-form
    display: flex
    flex: 1 1 100%
    flex-direction: column
    max-height: 100%

.private-panel__container--with-body
  height: 0  // #2597

.private-panel__header
  +display(flex)
  +flex-direction(column)
  background-color: $modal-header-background-color
  color: $modal-header-foreground-color
  // use a negative margin for backwards compatibility
  margin-top: rem-calc($modal-body-padding-y * -1)

  + .private-panel__section
    padding-top: rem-calc($modal-panel-padding-y)

  h1, h2, h3, h4, h5, h6
    +m-all-0

.private-panel__section
  padding-left: rem-calc($modal-body-padding-x)
  padding-right: rem-calc($modal-body-padding-x)
  padding-top: 0

  // in order for the sticky footer to work in a backwards-compatible way
  // without a `__body`, we're only setting flex-grow for the last `__section`.
  // otherwise, the height distributes in a non-ideal way
  &:last-of-type
    +flex-grow(1)

.private-panel__body
  +flex(1 1 100%)
  min-height: auto
  overflow: auto
  padding: 0
  position: relative

  .private-panel__section
    +flex-grow(0)
    padding-top: rem-calc($modal-panel-padding-y)

.private-panel__footer
  background-color: $panel-footer-background-color
  border-top: 1px solid $panel-footer-border-color
  z-index: $mercury-layer

.private-panel__steps
  margin-bottom: 0
  padding-top: rem-calc($modal-panel-footer-padding-y)


// UIPanel within a UIPanelNavigator
.private-panel-navigator--child
  &.private-panel
    box-shadow: none !important
    flex-shrink: 0
    position: relative
