@import HubStyle/stylesheets/hubstyle/base

// Header details block
// Variation of a media object, but with strong responsive opinions

// #3584: fix for IE11 blowing up height w/ child image
// http://connect.microsoft.com/IE/feedbackdetail/view/891815/ie11-flexbox-
// items-do-not-vertically-shrink-when-child-images-vertically-shrink

$details-padding-x: 24px
$details-padding-y: 16px

.private-header-details.private-header-details
  margin-left: rem-calc($details-padding-x * -1)
  max-width: none
  min-height: 1px // #3584
  padding: 0 rem-calc($details-padding-x) 0 0
  width: calc(100% + #{($details-padding-x * 2)})

.private-header-details__content,
.private-header-details__media
  margin: 0 0 rem-calc($details-padding-y) rem-calc($details-padding-x)

.private-header--with-details:not(.private-header--with-tabs)
  .private-header-details
    padding-top: rem-calc($header-padding-bottom)
