@import HubStyle/stylesheets/hubstyle/base

/// Breadcrumbs

$breadcrumbs-padding-top: 12px

.private-breadcrumbs
  color: $color-slinky
  margin-top: rem-calc(16px)
  overflow: hidden
  padding: 0 rem-calc($layout-padding-x)
  position: relative
  text-overflow: ellipsis
  z-index: $mercury-layer

  .private-breadcrumbs__arrow
    +set-font-size(10px)
    color: $color-eerie
    line-height: 0
    margin-left: rem-calc($base-icon-spacing-x, em)
    margin-right: rem-calc($base-icon-spacing-x, em)
    vertical-align: middle

    &.private-breadcrumbs__arrow--back.private-breadcrumbs__arrow--back
      color: inherit

  > :first-child
    .private-breadcrumbs__arrow
      margin: 0
      margin-right: rem-calc($base-icon-spacing-x, em)

  .private-breadcrumbs__item
    +set-font-size($microcopy-size)
    position: relative
    vertical-align: middle

    + .private-breadcrumbs__item
      margin-left: 0

.private-breadcrumbs + .private-header
  padding-top: 0

// The v2+ API: breadcrumbs are in UIHeader
.private-header
  .private-breadcrumbs
    +p-x-0
    +m-top-0

.private-breadcrumbs--flush
  padding-left: 0
  padding-right: 0

.private-breadcrumbs--no-wrap
  white-space: nowrap
