/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Popovers

.private-popover
  max-width: 100vw
  position: relative

  &.private-popover--default
    +dropdown-menu-appearance

  &.private-tooltip--longform
    +dropdown-menu-appearance
    line-height: 1.5

  &.private-popover--shepherd
    +dropdown-menu-appearance
    background-color: $color-olaf
    border: 0
    box-shadow: 0 0 0 1px rgba($color-slinky, .3), 0 1px 24px 0 rgba($color-monolith, .08)

  &.private-popover--tooltip
    +set-border-radius
    +set-font-size($tooltip-font-size)
    background-color: $tooltip-default-background-color
    color: $color-olaf
    max-width: rem-calc($tooltip-max-width)

  &.private-popover--tooltip-danger
    background-color: $tooltip-danger-background-color

.private-popover__inner
  border-radius: inherit

// Inset pinned dropdowns so that they don't quite touch the screen edge
.tether-pinned-left .private-popover
  left: 1px

.tether-pinned-right .private-popover
  right: 1px
