/* @bend-expect trellis-tokenized */
// Won't tokenize because these styles need to be static (i.e. values should't change)

// IE8+

%sr-only
  position: absolute
  top: 0
  width: 1px
  height: 1px
  margin: -1px
  padding: 0
  overflow: hidden
  clip: rect(0, 0, 0, 0)
  border: 0

%sr-only-focusable
  &:active,
  &:focus
    position: static
    top: auto
    width: auto
    height: auto
    margin: 0
    overflow: visible
    clip: auto

