/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

/// Two-column, divided inner layout with scrolling

$layout-column-padding: 20px
$scrolling-columns-height: 400px // @todo: convert to token

.private-scroll-columns
  +align-self(stretch)
  +display(flex)
  height: 100%
  margin-left: rem-calc($layout-column-padding * -1)
  margin-top: rem-calc($layout-column-padding * -1)
  width: calc(100% + #{rem-calc($layout-column-padding)})

/// Spacing adjustment for multiple columns
.private-scroll-columns--inset
  margin-top: rem-calc($layout-column-padding)
  min-width: 0
  padding-left: rem-calc($layout-column-padding)

  &.private-scroll-columns__column
    height: auto

  /// Opinions about padding within the multicolumn context
  &.private-scroll-columns--left
    padding-right: rem-calc($layout-column-padding)

  &.private-scroll-columns--right
    border-left: 1px solid $hr-border-color

  .private-scroll-columns__header
    padding-bottom: rem-calc($layout-column-padding)

  .private-scroll-columns__footer
    padding-top: rem-calc($layout-column-padding)

/// Scrolling column can be standalone and has no strong opinions about padding

.private-scroll-columns__column
  +display(flex)
  +flex-direction(column)
  height: 100%
  position: relative

.private-scroll-columns__column--stretch
  +display(flex)
  +flex(1 1 auto)
  height: 100%

.private-scroll-columns__inner
  +display(flex)
  +flex-direction(column)
  height: 100%
  /// Fixes bug in Firefox with nested flex + overflow
  min-height: 0
  /// ^ @link: https://stackoverflow.com/a/28639686
  width: 100%


/// Within a UIModal, this layout forces a max-height of 400px above a
/// threshold of 830px wide to scroll within the columns and shows a
/// vertical divider line. Below that, it scrolls the entire contents
/// in stacked columns.

@media only screen and (min-width: rem-calc(830px))
  .private-modal--is-popup
    .private-scroll-columns
      height: auto
      max-height: rem-calc($scrolling-columns-height)
      min-height: 0

    .private-scroll-columns__column
      height: auto

    .private-scroll-columns__inner
      +flex-basis($scrolling-columns-height)                    /// #3138

    .private-scroll-columns--grow
      &.private-scroll-columns--grow
        max-height: calc(100% + #{rem-calc($layout-column-padding)})

      .private-scroll-columns__inner
        +flex-basis(auto)

    /// Overrides max-height
    .private-scroll-columns--custom-height
      max-height: 100%

      .private-scroll-columns__inner
        +flex-basis(auto)

    /// this prioritizes scrolling on a column thus letting the other column set
    /// the overall height
    .private-scroll-columns__column--overflows
      .private-scroll-columns__inner
        height: 100%
        position: absolute
        width: calc(100% - #{$layout-column-padding})

      &.private-scroll-columns--inset
        /// Opinions about padding within the multicolumn context
        &.private-scroll-columns--left
          .private-scroll-columns__inner
            width: calc(100% - #{$layout-column-padding * 2})

/// Overrides at or below the `small` viewport size to stop wrapping, normalize
/// padding adjustments, remove border, and maximize height

@media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
  .private-modal--is-popup
    .private-scroll-columns
      +flex-wrap(wrap)

    .private-scroll-columns--inset.private-scroll-columns--left
      padding-right: 0

    .private-scroll-columns--right
      border-left: 0

    .private-scroll-columns--custom-height
      .private-scroll-columns__inner
        max-height: 100% !important

/// Within a UIPanel
/// Distinct from a modal because we want the full-height effect

.private-panel__container--with-body
  .private-scroll-columns__column
    height: 100%
    min-height: 0
    width: 100%

  .private-scroll-columns__inner
    +flex-basis(auto)                                           /// #3138
