/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Labels

.private-form__label
  +input-metrics($label-default-padding-top, 0, $base-spacing-x, $label-default-padding-bottom, $responsive: false)
  +set-font-family(medium)
  +set-font-size($base-font-size)
  +user-select(none)
  display: inline-block
  vertical-align: $input-vertical-alignment

  &[for]
    cursor: default

.private-form__label--readonly
  &[for]
    cursor: default

.private-form__label--required
  &::after
    content: '\0000a0*'

.private-form__label--small
  +set-font-size(12px)

.private-form__label--with-tooltip
  padding-right: 0

.private-form__inlinehelp
  +display(flex)
  cursor: inherit
  font-weight: $web-font-medium-weight

// Formatting for validation message below form fields
.private-form__validation,
.private-form__description
  display: block
  font-weight: $web-font-medium-weight
  margin-top: rem-calc($form-set-validation-top-spacing)
  padding-bottom: rem-calc(1px) // <- correction for line-height change in #4064

.private-form__set--floating
  .private-form__validation
    margin-left: rem-calc(12px)

.private-form__label--floating
  +set-font-size(12px)
  +transition(all $micro-transition-timing ease-out)
  +user-select(none)
  color: $color-slinky
  font-weight: $web-font-regular-weight
  max-width: 100%
  position: absolute
  top: rem-calc(-6px)
  z-index: $mercury-layer

  &.private-form__label--required
    &::after
      content: none

.private-form__label--floating-as-placeholder
  +set-font-size($input-sm-font-size)
  // Roughly center if the form contains a standard input
  top: rem-calc(9px)

  + .private-button__dropdown-opener
    opacity: 0
