/* @bend-expect trellis-tokenized */

// IE8+
@import cssUtils/stylesheets/utility-classes/extendables/margin-padding-positioning

.list-unstyled
  @extend %p-left-0
  list-style: none !important

.list-inline
  display: inline-block !important

  > li
    display: inline-block !important
    @extend %p-left-1
    @extend %p-right-1
