/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base


/// Multicolumn layout object:
/// Provides a way to get table-like layout, with elastic columns

$stacked-breakpoint: 576px

.private-multicolumn
  +set-table-border-color
  background-color: $color-olaf
  border-width: 1px
  width: 100%

.private-multicolumn__group
  +set-table-border-color
  border-width: 1px 0 0

.private-multicolumn__item
  padding: rem-calc($table-default-padding-y) rem-calc($table-default-padding-x)

.private-multicolumn__item__title
  +set-heading(heading-5)
  margin-bottom: rem-calc(4px)

.private-multicolumn__section
  .private-multicolumn__group
    &:not(:first-child)
      border-color: transparent

      // sass-lint:disable nesting-depth
      .private-multicolumn__item
        border-color: transparent

      .private-multicolumn__item__title
        +set-heading(heading-6)
        margin-bottom: rem-calc(4px)
      // sass-lint:enable nesting-depth

/// @todo: make these into alignment utility modifiers

.private-multicolumn--align-top
  vertical-align: top

.private-multicolumn--align-middle
  vertical-align: middle

.private-multicolumn--align-bottom
  vertical-align: bottom

/// handles case of multi-column layout w/o borders or outer padding

.private-multicolumn--flush
  border-color: transparent

  // sass-lint:disable nesting-depth
  > .private-multicolumn__group,
  > .private-multicolumn__section
    &:first-child
      .private-multicolumn__item
        padding-top: 0

    &:last-child
      .private-multicolumn__item
        padding-bottom: 0
  // sass-lint:enable nesting-depth

/// 576px is repeated from cssUtils constants for the moment

@media only screen and (min-width: rem-calc($stacked-breakpoint))
  .private-multicolumn
    display: table

    > .private-multicolumn__group,
    > .private-multicolumn__section
      &:first-child
        // sass-lint:disable nesting-depth
        .private-multicolumn__item
          border-color: transparent
        // sass-lint:enable nesting-depth

  .private-multicolumn__section
    display: table-row-group

  .private-multicolumn__group
    display: table-row

  .private-multicolumn--flush
    .private-multicolumn__item
      &:first-child
        padding-left: 0

      &:last-child
        padding-right: 0

  .private-multicolumn__item
    +set-table-border-color
    border-width: 1px 0 0
    display: table-cell

    &:last-child
      &:not(:first-child)
        text-align: right

@media only screen and (max-width: rem-calc($stacked-breakpoint - 1))

  .private-multicolumn
    > .private-multicolumn__group
      &:first-child
        border-color: transparent

  .private-multicolumn__item
    border-color: transparent
    width: 100% !important

  .private-multicolumn--flush
    .private-multicolumn__item
      padding-left: 0
      padding-right: 0
