/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

$grippie-offset-x: 26px
$content-padding: 10px
$border-width: 1px

.private-moveable-area-container
  padding: rem-calc($border-width)
  padding-left: rem-calc($grippie-offset-x + $border-width)

.private-moveable-area-container--no-draggable
  margin-left: rem-calc($grippie-offset-x + $border-width)
  padding-left: 0

.private-moveable-area-container--container-draggable
  +set-grab-cursor

.private-moveable-area-container--visible
  padding: 0

  .private-moveable-area
    border-width: $border-width
  .private-moveable-area__grippie
    background-color: $color-calypso
    width: rem-calc($grippie-offset-x)

.private-moveable-area-container--visible--draggable
  .private-moveable-area__content
    padding-left: rem-calc($grippie-offset-x + $content-padding)

.private-moveable-area-container--visible--no-draggable
  margin-left: rem-calc($grippie-offset-x)

.private-moveable-area
  border: 0 solid $color-calypso
  position: relative

  // overwrite default top padding for label
  .private-form__label
    padding-top: 0

.private-moveable-area--selected
  background-color: $color-koala

.private-moveable-area__grippie
  height: 100%
  left: 0
  position: absolute

.private-moveable-area__content
  padding: rem-calc($content-padding)
