/* @bend-expect trellis-tokenized */

/// placing all design token references here
/// paves the way for Sass-loader with Webpack

/// keep this as only variables and mixins to prevent duplicate rules being rendered
/// variables should use $dash-case (https://github.com/airbnb/css#variables)

@import 'HubStyleTokens/stylesheets/tokens';
@import 'HubStyle/stylesheets/hubstyle/utils/functions/rem-calc';
@import 'HubStyle/stylesheets/hubstyle/utils/mixins/set-font-family';
@import 'HubStyle/stylesheets/hubstyle/utils/mixins/set-placeholder-style';

@import 'cssUtils/stylesheets/mixins/background-linear-gradient';
@import 'cssUtils/stylesheets/mixins/display';
@import 'cssUtils/stylesheets/mixins/flex-box';
@import 'cssUtils/stylesheets/mixins/font-smoothing';
@import 'cssUtils/stylesheets/mixins/placeholder';
@import 'cssUtils/stylesheets/mixins/transform';
@import 'cssUtils/stylesheets/mixins/transition';
@import 'cssUtils/stylesheets/mixins/user-select';
@import 'cssUtils/stylesheets/utility-classes/utility-mixins/margin-padding-positioning';
@import 'cssUtils/stylesheets/utility-classes/utility-mixins/sr-only';
@import 'cssUtils/stylesheets/utility-classes/utility-mixins/text-align';

/// Map of headings
/// key:      $size       selector  weight                transform
/// ---------|-----------|---------|---------------------|-----------------

$heading-sizes: (
  heading-1: (
    $trellis-typography-heading-600-font-size,
    h1,
    $trellis-typography-heading-600-font-weight,
    none,
  ),
  heading-2: (
    $trellis-typography-heading-500-font-size,
    h2,
    $trellis-typography-heading-500-font-weight,
    none,
  ),
  heading-3: (
    $trellis-typography-heading-400-font-size,
    h3,
    $trellis-typography-heading-400-font-weight,
    none,
  ),
  heading-4: (
    $trellis-typography-heading-300-font-size,
    h4,
    $trellis-typography-heading-300-font-weight,
    none,
  ),
  heading-5: (
    $trellis-typography-heading-100-font-size,
    h5,
    $trellis-typography-heading-100-font-weight,
    none,
  ),
  heading-6: (
    $trellis-typography-heading-50-font-size,
    h6,
    $trellis-typography-heading-50-font-weight,
    $heading-case-6,
  ),
  heading-7: (
    $trellis-typography-heading-50-font-size,
    null,
    $trellis-typography-heading-50-font-weight,
    none,
  ),
);

/// Map of named viewport breakpoints

$grid-breakpoints: (
  sm: $grid-breakpoint-small,
  md: $grid-breakpoint-medium,
  lg: $grid-breakpoint-large,
  xl: $grid-breakpoint-extra-large,
);

/// @access public
/// @group helpers
/// @name make-heading

@mixin make-heading(
  $size,
  $weight: $trellis-typography-heading-1000-font-weight,
  $case: null,
  $important: false
) {
  @if $important == true {
    $important: ' !important';
  } @else {
    $important: null;
  }
  @include set-font-family-tokenized-weight($weight, $important: $important);
  @include font-smoothing($heading-font-smoothing, $important: $important);
  @include single-direction-module-spacing;
  display: block;
  font-size: rem-calc($size) #{$important};
  line-height: normal #{$important};
  text-transform: $case #{$important};
}

@mixin set-heading($size) {
  @include make-heading(
    nth(map-get($heading-sizes, $size), 1),
    nth(map-get($heading-sizes, $size), 3),
    nth(map-get($heading-sizes, $size), 4)
  );
}

/// @access public
/// @group helpers
/// @name font-size
/// @link http://www.stubbornella.org/content/2013/07/01/easy-peasy-rem-conversion-with-sass/

@mixin set-font-size($size) {
  font-size: $size;
}

/// @access public
/// @group helpers
/// @name set-font-shorthand

@mixin set-font-shorthand($size) {
  @include set-font-size($size);
  line-height: rem-calc($size);
}

/// @access public
/// @group helpers
/// @name set-disabled-style

@mixin set-disabled-style {
  background-color: $trellis-color-fill-field-disabled !important;
  border-color: $trellis-color-border-core-disabled !important;
  color: $color-button-disabled-text !important; // TODO: Needs Trellis token
  cursor: not-allowed;
}

/// @access public
/// @group helpers
/// @name set-readonly-style

@mixin set-readonly-style {
  background-color: $trellis-color-fill-field-disabled !important;
  border-color: $trellis-color-border-core-disabled !important;
  color: $trellis-color-text-core-default !important;
  cursor: auto;
}

/// @access public
/// @group helpers
/// @name link-style

@mixin link-style(
  $color: $trellis-color-text-interactive-default,
  $hover-color: $trellis-color-text-interactive-default-hover,
  $active-color: $trellis-color-text-interactive-default-pressed,
  $weight: demibold
) {
  @include set-font-family($weight);
  @include font-smoothing(antialiased);
  @include ui-transition;
  color: $color;
  text-decoration: none;

  &:hover {
    color: $hover-color;
    text-decoration: underline;
  }

  &:active {
    color: $active-color;
  }
}

/// @access public
/// @group helpers
/// @name link-style-on-dark

@mixin link-style-on-dark(
  $color: $trellis-color-text-interactive-on-fill,
  $hover-color: $trellis-color-text-interactive-default-pressed,
  // TODO: Needs Trellis text color token
  $active-color: $trellis-color-fill-primary-disabled,
  $weight: demibold
) {
  @include link-style($color, $hover-color, $active-color, $weight);
  text-decoration: underline;
}

/// @access public
/// @group helpers
/// @name link-style-on-bright

@mixin link-style-on-bright(
  $color: $trellis-color-text-core-default,
  $hover-color: $trellis-color-text-core-default,
  $active-color: $trellis-color-text-core-default,
  $weight: demibold
) {
  @include link-style($color, $hover-color, $active-color, $weight);
  @include font-smoothing(auto);
  text-decoration: underline;
}

/// @access public
/// @group helpers
/// @name link-style-danger

@mixin link-style-danger(
  $color: $trellis-color-text-alert-default,
  // TODO: The hover state color in Trellis doesn't match UIComponents
  $hover-color: $trellis-color-text-alert-pressed,
  $active-color: $trellis-color-text-alert-pressed
) {
  @include link-style($color, $hover-color, $active-color);
}

/// @access public
/// @group helpers
/// @name link-style-mention

@mixin link-style-mention(
  // TODO: Needs Trellis text token
  $color: $trellis-color-fill-accent-neutral-default,
  $hover-color: $trellis-color-text-core-default,
  $active-color: $trellis-color-text-core-default
) {
  @include link-style($color, $hover-color, $active-color);
  @include set-font-family(bold);
}

/// @access public
/// @group helpers
/// @name icon-within-link

@mixin icon-within-link($color: $trellis-color-icon-core-subtle) {
  color: $color;
}

/// @access public
/// @group helpers
/// @name input-metrics
/// responsive foundation for buttons and input fields

@mixin input-metrics(
  $padding-vertical: $trellis-space-300,
  $padding-horizontal: $trellis-space-600,
  $padding-right: $padding-horizontal,
  $padding-bottom: $padding-vertical,
  $responsive: true
) {
  @if $padding-bottom {
    padding-bottom: rem-calc($padding-bottom);
  } @else {
    padding-bottom: rem-calc($padding-vertical);
  }

  padding-left: rem-calc($padding-horizontal);

  @if $padding-right {
    padding-right: rem-calc($padding-right);
  } @else {
    padding-right: rem-calc($padding-horizontal);
  }

  padding-top: rem-calc($padding-vertical);

  /// \_( mobile-last )_/ -- less resetting that way
  /// leave this in as a fallback for responsive behavior not covered by
  /// container queries

  @if $responsive == true {
    @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm))) {
      width: 100%;
    }
  }
}

/// @access public
/// @group helpers
/// @name set-border-radius

@mixin set-border-radius($radius: $trellis-border-radius-100) {
  border-radius: rem-calc($radius);
}

/// @access public
/// @group helpers
/// @name set-transparent

@mixin set-transparent {
  background-color: $trellis-color-fill-transparent-default;
  border-color: $trellis-color-fill-transparent-default; // We only have fill tokens for transparent colors at the moment
}

/// @access public
/// @group helpers
/// @name button-borders

@mixin button-borders(
  $width: $trellis-border-width-100,
  $radius: $trellis-border-radius-100
) {
  @include set-border-radius($radius);
  border-style: solid;
  border-width: $width;
}

@mixin input-borders(
  $border-width: $trellis-border-width-100,
  $border-color: $trellis-color-border-core-default
) {
  @include set-border-radius;
  border: $border-width solid $border-color;
}

/// @access public
/// @group helpers
/// @name ui-transition

@mixin ui-transition($timing: $micro-transition-timing, $property: all) {
  transition: $property $timing ease-out;
}

/// @access public
/// @group helpers
/// @name ui-focus
/// Won't tokenize because we want to keep the focus ring blue

@mixin ui-focus(
  $shadow-color: $color-focus-ring-base,
  $border-color: $color-focus-ring-base,
  $important: false
) {
  @if $important == true {
    $important: ' !important';
  } @else {
    $important: null;
  }

  border-color: rgba($border-color, $focus-ring-border-alpha) #{$important};
  box-shadow:
    $focus-ring-shadow-sizes rgba($shadow-color, $focus-ring-shadow-alpha),
    $focus-ring-shadow-last-sizes $shadow-color #{$important};
  outline: 0;
}

/// @access public
/// @group helpers
/// @name ui-layer-shadow

@mixin ui-layer-shadow {
  box-shadow: $trellis-box-shadow-400;
}

/// @access public
/// @group helpers
/// @name background-image-diagonal-stripes

@mixin background-image-diagonal-stripes(
  $angle: 135deg,
  $color1: $trellis-color-fill-alert-default,
  $color2: $trellis-color-fill-alert-hover,
  $stripe-size: 24px
) {
  background-image: repeating-linear-gradient(
    $angle,
    $color1,
    $color1 $stripe-size,
    $color2 $stripe-size,
    $color2 $stripe-size * 2
  );
}

/// @access public
/// @group helpers
/// @name make-triangle

@mixin make-triangle($color: currentColor, $size: 4px, $direction: 'down') {
  border-color: transparent;
  border-style: solid;
  border-width: rem-calc($size, em);
  display: inline-block;
  vertical-align: middle;

  @if $direction == 'down' {
    border-top-color: $color;
    margin-top: rem-calc($size, em);
  }

  @if $direction == 'up' {
    border-bottom-color: $color;
    margin-top: rem-calc($size * -1, em);
  }

  @if $direction == 'left' {
    border-right-color: $color;
    margin-left: rem-calc($size/2 * -1, em);
    margin-top: rem-calc($size/2 * -1, em);
  }

  @if $direction == 'right' {
    border-left-color: $color;
    margin-left: rem-calc($size/2, em);
    margin-top: rem-calc($size/2 * -1, em);
  }
}

/// @access public
/// @group helpers
/// @name single-direction-module-spacing

@mixin single-direction-module-spacing($size: $trellis-space-400) {
  margin-bottom: rem-calc($size);
  margin-top: $trellis-space-0;
}

/// @access public
/// @group helpers
/// @name input-appearance

@mixin input-appearance {
  @include input-borders;
  @include set-font-size($trellis-typography-body-200-font-size);
  @include ui-transition;
  background-color: $trellis-color-fill-field-default;
  color: $trellis-color-text-core-default;
  display: block;
  height: rem-calc($input-default-height);

  // Can't be tokenized to `$trellis-typography-body-200-line-height` because the larger line height causes a height change in some components
  line-height: rem-calc(22px);

  text-align: left;
  vertical-align: $input-vertical-alignment;
  width: 100%;
}

/// @access public
/// @group helpers
/// @name select-appearance

@mixin small-input-appearance {
  @include set-font-size($trellis-typography-body-100-font-size);
  height: rem-calc($input-sm-height);
  padding-bottom: rem-calc($trellis-space-100);
  padding-top: rem-calc($trellis-space-100);
}

/// @access public
/// @group helpers
/// @name set-grab-cursor

@mixin set-grab-cursor {
  cursor: move; // fallback for IE11
  cursor: grab;
}

/// @access public
/// @group helpers
/// @name set-grabbing-cursor

@mixin set-grabbing-cursor {
  cursor: move; // fallback for IE11
  cursor: grabbing;
}

/// @access public
/// @group helpers
/// @name dropdown-menu-appearance

@mixin dropdown-menu-appearance {
  @include set-border-radius;
  @include ui-layer-shadow;
  background-color: $trellis-color-fill-surface-default;
  border: $trellis-border-width-100 solid $trellis-color-border-core-default;
}

/// @access public
/// @group helpers
/// @name dropdown-item-appearance

@mixin dropdown-item-appearance {
  @include font-smoothing(auto);
  @include set-font-family(regular);
  @include set-font-size($trellis-typography-body-100-font-size);
  @include user-select(none);
  border: $trellis-border-width-0;
  border-radius: $trellis-border-radius-0;
  color: $trellis-color-text-core-default;
  display: block;
  min-height: rem-calc($dropdown-item-default-height);
  padding: rem-calc($trellis-space-200) rem-calc($trellis-space-500);
  text-align: left;
  width: 100%;
}

/// @access public
/// @group helpers
/// @name dropdown-item-hover

@mixin dropdown-item-hover {
  background: $trellis-color-fill-field-hover;
  color: $trellis-color-text-core-default;
}

/// @access public
/// @group helpers
/// @name dropdown-group-label

@mixin dropdown-group-label {
  @include set-font-family(medium);
  @include set-heading(heading-7);
  margin: $trellis-space-0;
  min-height: $dropdown-item-default-height;
  padding: rem-calc(calc($trellis-space-200 + $trellis-space-100))
    rem-calc($trellis-space-500) rem-calc($trellis-space-200);
}

/// @access public
/// @group helpers
/// @name set-table-border-color

@mixin set-table-border-color {
  border-color: $trellis-color-border-core-subtle;
  border-style: solid;
}

/// @access public
/// @group helpers
/// @name hyphenate-words

@mixin hyphenate-words($hyphenate: auto, $important: false) {
  @if $important == true {
    $important: ' !important';
  } @else {
    $important: null;
  }

  // Adds a hyphen where the word breaks, if supported (No Blink)
  -ms-hyphens: $hyphenate #{$important};
  -webkit-hyphens: $hyphenate #{$important};
  hyphens: $hyphenate #{$important};
}

/// @access public
/// @group helpers
/// @name search-icon-appearance

@mixin search-icon-appearance($size: $trellis-typography-body-200-font-size) {
  color: $trellis-color-icon-interactive-default;
  font-size: rem-calc($size);
  line-height: rem-calc($size);
  margin-left: rem-calc($size);
  margin-right: rem-calc($size);
  margin-top: rem-calc(calc($size * 3 / 4));
  pointer-events: none;
}

/// @access public
/// @group helpers
/// @name set-template-max-width

@mixin set-template-max-width {
  margin-left: auto;
  margin-right: auto;

  // Won't tokenize because this is a size property
  // TODO https://git.hubteam.com/HubSpot/foundations-components/issues/5943
  max-width: rem-calc($layout-max-width + ($layout-padding-x * 2));

  width: 100%;
}
