@import HubStyle/stylesheets/hubstyle/base

/// Internal editor template layout

.private-editor__inner
  +align-items(stretch)
  +display(flex)
  height: 100%
  left: 0
  position: absolute
  top: 0
  width: 100%

.private-editor__sidebar
  +flex-shrink(0)
  box-shadow: rem-calc(1px) 0 rem-calc(3px) 0 rgba($editor-layout-shadow-color, .25)
  z-index: $mercury-layer

.private-editor__canvas
  +flex(1)
  padding: rem-calc($layout-editor-padding)
  position: relative

/// extend main container to full-height of container

$editor-padding-offset: rem-calc($layout-editor-padding * 2)

.private-editor__main
  &.private-editor__main
    height: calc(100% - #{$editor-padding-offset})
    padding-bottom: 0
    position: absolute
    width: calc(100% - #{$editor-padding-offset})

.private-editor__canvas--flush
  padding: 0

  .private-editor__main
    &.private-editor__main
      height: calc(100%)
      width: calc(100%)
