@import sassPrefix/_font-smoothing.sass

[class*='private-icon__']
  +font-smoothing
  font-family: 'spacesword' !important
  font-feature-settings: 'liga'
  font-variant: normal
  font-weight: normal
  letter-spacing: 0
  text-transform: none

[class*='private-icon__'][class*='high']
  font-weight: bold
