/* @bend-expect trellis-tokenized */

@import 'HubStyleTokens/stylesheets/tokens';

/// @access public
/// @group helpers
/// @name set-font-family
/// @deprecated Use `set-font-family-tokenized-weight` instead.

@mixin set-font-family($weight: regular, $important: null) {
  font-family: $trellis-typography-body-100-font-family #{$important};

  $font-weight: null;
  @if $weight == bold {
    $font-weight: $trellis-typography-heading-600-font-weight;
  } @else if $weight == demibold {
    $font-weight: $trellis-typography-heading-100-font-weight;
  } @else if $weight == medium {
    $font-weight: $trellis-typography-heading-400-font-weight;
  } @else if $weight == italic {
    $font-weight: $trellis-typography-body-100-font-weight;
    font-style: italic;
  } @else {
    $font-weight: $trellis-typography-body-100-font-weight;
  }
  font-weight: $font-weight #{$important};
}

/// @access public
/// @group helpers
/// @name set-font-family-tokenized-weight

@mixin set-font-family-tokenized-weight(
  $weight: $trellis-typography-body-100-font-weight,
  $important: null,
  $italic: false
) {
  font-family: $trellis-typography-body-100-font-family #{$important};
  font-weight: $weight #{$important};

  @if $italic {
    font-style: italic;
  }
}
