/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
/// @description This file should never be directly imported in product code. It's included in a bundle for application code only (not library code). The goal is to only have CSS Variables defined once per page in the product (plus each iframe).
////

@import 'HubStyleTokens/stylesheets/tokens';

:root {
  #{--uicomponents-font-family}: '#{$priority-web-font}', $fallback-font-stack; // The #{} surrounding the variable name is needed to prevent sass-lint from crashing (see https://github.com/sasstools/sass-lint/issues/877#issuecomment-267176435)
  #{--uicomponents-global-nav-height}: 54px;
}
