/* @bend-expect trellis-tokenized */

// Need for FF 50, chrome 55, safari 10, no IE support
=font-smoothing($rendering: antialiased, $important: null)
  @if $important
    $important: ' !important'

  -webkit-font-smoothing: $rendering #{$important}
  @if ($rendering == 'antialiased' )
    -moz-osx-font-smoothing: grayscale #{$important}
  @else
    -moz-osx-font-smoothing: $rendering #{$important}
