/* @bend-expect trellis-tokenized */

// IE8+

@import sassPrefix/hyphens

=truncate-text
  +hyphens(none)
  overflow: hidden
  text-overflow: ellipsis
  white-space: nowrap
  width: 100%
  @content

=truncate-text-weak
  +hyphens(none)
  overflow: hidden
  text-overflow: ellipsis
  white-space: nowrap
  @content
