@import HubStyle/stylesheets/hubstyle/base

/// Layout templates
/// Rules supporting various template options including:
///
/// - [x] centered max-width
/// - [x] full-width
/// - [x] carded background
/// - [x] full-height
/// - [x] level zero marketing layout (narrow)
/// - [x] responsive gutters

/// top-level body selector that is added by using prescribed layouts

/// Modifier to suppress navigation
// sass-lint:disable no-ids
.space-sword--editor
  #hs-nav-v4, #placeholder-vertical-nav, #trial-banner-block
    display: none
  .page, .app
    margin-left: 0
    width: auto

// sass-lint:enable no-ids

.private-template--dark
  background-color: $color-gypsum

.private-template__title-badge
  +m-x-2
  vertical-align: middle

/// Modifiers

.private-template--centered
  .private-template__section
    +set-template-max-width

.private-template--narrow
  .private-template__section
    max-width: rem-calc($layout-narrow-width)

/// Mixin to share responsive styles with a className modifier

=section-gutters($breakpoint: null)
  $divisor: 1

  @if $breakpoint == sm
    $divisor: 4
  @else if $breakpoint == md
    $divisor: 2

  .private-template__section
    padding-left: rem-calc($layout-padding-x / $divisor)
    padding-right: rem-calc($layout-padding-x / $divisor)


/// Subcomponents

/// Styles for .private-template__section in mixin below
+section-gutters

.private-template__section--header
  // address flex-bug seen in older Safari
  +flex-shrink(0)

/// editor layout exceptions
.private-editor
  height: 100vh

  .private-template__section--header
    padding-left: 0
    padding-right: 0
    z-index: $venus-layer

  .private-template__section--stretch
    +display(flex)
    +flex-direction(column)
    +flex-grow(1)
    +flex-shrink(1)
    +flex-basis(0)
    height: auto

  .private-template__section--flush
    +flex-grow(1)
    position: relative

.private-template__section--stretch
  +flex(1 0 auto)

.private-template__section--dark
  background-color: $color-gypsum

.private-template__section--divided
  border-top: 1px solid $template-section-divider-border-color

.private-template__section--spaced-vertical
  padding-top: rem-calc($layout-main-padding-top)

.private-template__section--flush
  height: 100%
  padding-left: 0 !important
  padding-right: 0 !important

/// Responsive gutters




@media only screen and (max-width: rem-calc(map-get($grid-breakpoints, md)))
  +section-gutters(md)

@media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
  +section-gutters(sm)

/// Cascade order important to override media queries

.private-template--desktop
  +section-gutters

.private-template--mobile
  +section-gutters(sm)

.private-template--tablet
  +section-gutters(md)


