/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Modals

/// Specs:
/// * only use <UIModalHeader> to get default gradient top bar
/// * header-less modals just omit <UIModalHeader>
/// * button alignment can be handled at the call-site
/// * internal layout will be handled by layout concerns (UIGrid, UIMedia)
/// * short modals omit footers

/// Modal container
/// The container centers the modal and contains its detached close button
/// (if any) as a sibling. When the wrapper has `position: relative`, the
/// close button moves inside of the modal.

/// Modal root
/// @todo responsive behavior

$header-padding-y: 4px

=set-messaging-background
  background-color: $modal-header-background-color
  background-image: linear-gradient(-303deg, $modal-header-start-gradient 0%, $modal-header-middle-gradient 56%, $modal-header-end-gradient 100%)

=shared-wizard-footer-styles
  background-color: $panel-footer-background-color
  border-top: 1px solid $panel-footer-border-color

.private-modal
  +display(flex)
  +flex-direction(column)
  background-color: $color-olaf
  word-wrap: break-word

/// Modal sub-components

.private-modal__header
  +display(flex)
  +flex-direction(column)
  +flex-grow(0)
  +flex-shrink(0)
  +set-messaging-background
  color: $modal-header-foreground-color

  /// this is gross and unfortunate, but people are using whatever heading
  /// they want currently within UIModalHeader
  /// @todo use generated content in dev to warn about incorrect heading use
  h1, h2, h3, h4, h5, h6
    +set-heading(heading-4)
    +m-all-0
    width: 100%

.private-modal__header__inner
  +align-items(center)
  +display(flex)
  min-height: rem-calc($modal-header-height)
  padding: rem-calc($header-padding-y) rem-calc(56px) rem-calc($header-padding-y) rem-calc($modal-header-padding-x)

.private-modal__header--with-toolbar
  .private-modal__header__inner
    +align-items(flex-start)
    +justify-content(space-between)
    padding-right: rem-calc(24px)

    h2
      +align-self(center)
      margin-right: auto


.private-modal__header-image
  margin-bottom: rem-calc(40px)
  margin-left: auto
  margin-right: auto
  text-align: center

  @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
    margin-top: auto !important  // prevent illustration from going off-screen

.private-modal__body
  +set-font-size($alert-font-size)
  +flex-grow(1)
  padding: rem-calc($modal-body-padding-y) rem-calc($modal-body-padding-x)

  h1, h2, h3, h4, h5, h6
    color: inherit

.private-modal__footer
  +flex-grow(0)
  +flex-shrink(0)
  background-color: $modal-footer-background-color
  color: $base-font-color
  padding: rem-calc($modal-footer-padding-y) rem-calc($modal-footer-padding-x)
  text-align: left

  /// space adjacent children horizontally if above `sm` breakpoint
  > * + *
    @media only screen and (min-width: rem-calc(map-get($grid-breakpoints, sm) + 1))
      margin-left: rem-calc($base-spacing-x)

.private-modal > .private-form
  display: flex
  flex-direction: column
  flex-grow: 1


/// Modal variants

.private-modal--short
  .private-modal__body
    min-height: rem-calc($modal-short-height)

.private-modal--medium
  .private-modal__body
    min-height: rem-calc($modal-medium-height)

  // for backwards compat, keep medium/default combo at "auto" width :(
  &.private-modal--default
    .private-modal__body
      min-height: 0

.private-modal--tall
  .private-modal__body
    min-height: rem-calc($modal-tall-height)

.private-modal--default
  background-color: $modal-background-color
  text-align: left

  .private-modal__body
    padding: rem-calc($modal-header-padding-x)
    padding-bottom: 1px
    padding-top: rem-calc($modal-footer-padding-y)

.private-modal--conversational
  .private-modal__header
    background: none
    color: $color-obsidian
    padding-top: $modal-body-padding-y

    h2
      +set-heading(heading-1)

  .private-modal__header__inner
    min-height: 0
    padding-bottom: 0
    padding-top: 0

  .private-modal__header + .private-modal__body
    padding-top: 0

  .private-modal__close
    color: $modal-close-button-foreground-color

  .private-modal__header,
  .private-modal__body,
  .private-modal__footer
    text-align: center

  .private-modal__footer
    padding-top: 0

.private-modal--success
  +set-messaging-background
  color: $modal-success-foreground-color

.private-modal--info
  +set-messaging-background
  color: $modal-success-foreground-color
  text-align: center

.private-modal--upgrades
  +background-image-linear-gradient(-180deg, $modal-upgrades-start-gradient 20%, $modal-upgrades-end-gradient 71%)
  background-color: $modal-upgrades-start-gradient
  color: $modal-upgrades-foreground-color
  text-align: center

.private-modal--danger
  +background-image-diagonal-stripes
  color: $modal-danger-foreground-color

.private-modal--success,
.private-modal--danger,
.private-modal--info,
.private-modal--upgrades
  .private-modal__body
    h1, h2, h3, h4, h5, h6
      +set-heading(heading-1)
      color: $modal-heading-inverted-foreground-color

    .private-alert__title
      +set-heading(heading-5)
      margin-bottom: $alert-padding-y / 2

// Support new pattern for headers within color bleed modal types where the
// header is transparent and the main body is white. Relies on an adjacent
// sibling selector (+) of a body following a header for backwards compatibility
.private-modal--success,
.private-modal--danger,
.private-modal--info,
.private-modal--upgrades
  .private-modal__header
    background: transparent
    padding-bottom: rem-calc($modal-body-padding-y - $header-padding-y)
    padding-top: rem-calc($modal-body-padding-y - $header-padding-y)

    .private-modal__header__inner
      padding-right: rem-calc($modal-header-padding-x)

    h2
      +set-heading(heading-1)
      color: $modal-heading-inverted-foreground-color
      margin-bottom: 0


    + .private-modal__body
      background-color: $color-olaf
      color: $base-font-color
      margin-bottom: -1px
      padding-bottom: 1px
      padding-top: $modal-footer-padding-y

      h1, h2, h3, h4, h5, h6
        color: $base-font-color

/// modifier added explicitly by prop; okay to use !important
.private-modal--centered
  text-align: center !important

  // For any modal with the `centered` prop, this rule will apply the centering
  // behavior down to a nested dialog header, should it exist
  .private-modal__header__inner
    +justify-content(center)


/// Wizard styles (some of these need to override default modal styles)

.private-wizard
  &.private-modal__content-wrapper
    +display(flex)
    +flex-direction(column)
    +flex(1 1 0%)
    max-height: 100%  // #2083

  /// Overrides sticky header scenario
  .private-wizard__content-wrapper
    +flex(1 1 0%)
    min-height: auto
    overflow: auto

  /// Wizard footer modified to be less padded and gray
  .private-wizard__footer.private-modal__footer
    +shared-wizard-footer-styles
    padding: rem-calc(16px) rem-calc($modal-footer-padding-x)

.private-wizard__title
  margin-right: rem-calc($base-spacing-x)

.private-wizard__header-with-overview
  background-color: $wizard-header-background-color
  background-image: none
  color: inherit
  min-height: rem-calc(76px)  // binding only when overview is hidden
  padding: rem-calc(4px) rem-calc($modal-body-padding-x)

  .private-modal__header__inner
    padding: rem-calc(4px) 0

.private-wizard__header-step-count
  margin-left: rem-calc($base-spacing-x)

.private-wizard__body.private-modal__body
  padding-bottom: rem-calc($modal-body-padding-y)

.private-wizard__button--first
  order: 0

.private-wizard__cancel-button.private-wizard__cancel-button
  &:not(.private-wizard__button--first)
    margin-left: rem-calc($button-default-padding-x)

/// Overlay-related styles

.private-modal__container
  +m-auto-y-0
  min-height: 100%

  @media only screen and (max-width: rem-calc(800px))
    position: relative

/// Confirm dialog specific styles

.private-modal__matcher-outer
  margin-top: rem-calc(12px)
  width: 100%

  &:empty
    display: none

/// Rules that only apply to normal (non-fullscreen/sidebar) modals within overlays (see #3085)
.private-overlay
  .private-modal__container
    height: 100%

  .private-modal
    +set-border-radius($button-radius)

    // On smaller screens, go full-screen
    @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
      border-radius: 0
      min-height: 100%

  .private-modal__header
    border-top-left-radius: rem-calc($button-radius)
    border-top-right-radius: rem-calc($button-radius)

    @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
      border-top-left-radius: 0
      border-top-right-radius: 0

  .private-modal__footer
    border-bottom-left-radius: rem-calc($button-radius)
    border-bottom-right-radius: rem-calc($button-radius)

    @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
      border-bottom-left-radius: 0
      border-bottom-right-radius: 0

/// Shared scrolling styles

=shared-scrolling-parent
  +display(flex)
  +flex-direction(column)
  overflow: hidden  // #2074

=shared-scrolling-static-segment
  +flex(none)
  border-radius: 0

=shared-scrolling-body-segment
  +flex(1 1 0%)
  min-height: auto
  overflow: auto

=shared-scrolling-outer
  bottom: 0
  position: absolute
  right: 0
  top: 0


/// Lightbox styles

.private-modal--lightbox
  +shared-scrolling-outer
  background-color: $color-transparent
  color: $color-olaf
  display: block
  height: 100%
  overflow: hidden
  width: 100%

/// Fullscreen & sidebar modals share scrolling and flex layouts and override
/// rounded corners on headers and footers

/// Fullscreen modal styles

.private-modal.private-modal--fullscreen
  +shared-scrolling-outer

.private-modal--fullscreen
  +shared-scrolling-parent
  width: 100%

  .private-modal__header,
  .private-modal__footer
    +shared-scrolling-static-segment

  .private-modal__body,
  > .private-form
    +shared-scrolling-body-segment

.private-overlay .private-modal--fullscreen
  border-radius: 0

/// Sidebar modal styles

.private-modal.private-modal--sidebar
  +shared-scrolling-outer

.private-modal--sidebar
  +shared-scrolling-parent
  max-width: 100%
  min-width: 300px

  .private-modal__header,
  .private-modal__footer
    +shared-scrolling-static-segment

  .private-modal__body,
  > .private-form
    +shared-scrolling-body-segment

  .private-modal__body,
  .private-modal__footer
    padding-left: rem-calc($modal-panel-padding-x)
    padding-right: rem-calc($modal-panel-padding-x)

  .private-modal__footer
    +shared-wizard-footer-styles
    padding-bottom: rem-calc($modal-panel-footer-padding-y)
    padding-top: rem-calc($modal-panel-footer-padding-y)

.private-overlay .private-modal--sidebar
  border-radius: 0
