/* @bend-expect trellis-tokenized */

// IE8+

.sr-only
  position: absolute !important
  top: 0 !important
  width: 1px !important
  height: 1px !important
  margin: -1px !important
  padding: 0 !important // Won't tokenize because this always needs to be 0
  overflow: hidden !important
  clip: rect(0, 0, 0, 0) !important
  border: 0 !important // Won't tokenize because this always needs to be 0

.sr-only-focusable
  &:active,
  &:focus
    position: static !important
    top: auto !important
    width: auto !important
    height: auto !important
    margin: 0 !important // Won't tokenize because this always needs to be 0
    overflow: visible !important
    clip: auto !important
