/* @bend-expect trellis-tokenized */

@import sassPrefix/display
@import sassPrefix/flex-box

//
// Depends on display.sass
//

// No Support
// IE8+

// Prefixed support
// IE10
// Safari 7.1+

// iOS Safari 7.1+

// Unprefixed with problems
// No special stuff from here
// IE9+

// Full unprefixed support
// IE Edge
// Firefox
// Chrome 42+
// Safari 9

// http://caniuse.com/#search=flex
// https://developer.mozilla.org/en-US/docs/Web/CSS/flex


.no-shrink, .flex-shrink-0
  +display(flex)
  +flex-shrink(0 !important)

.flex-grow-1
  +display(flex)
  +flex-grow(1 !important)

.flex-no-wrap
  +display(flex)
  +flex-wrap(nowrap !important)

.flex-wrap
  +display(flex)
  +flex-wrap(wrap !important)

.flex-wrap-reverse
  +display(flex)
  +flex-wrap(wrap-reverse !important)

.flex-column
  +display(flex)
  +flex-direction(column !important)

.flex-row
  +display(flex)
  +flex-direction(row !important)


.justify-start
  +display(flex)
  +justify-content(flex-start !important)

.justify-end
  +display(flex)
  +justify-content(flex-end !important)

.justify-between
  +display(flex)
  +justify-content(space-between !important)

.justify-around
  +display(flex)
  +justify-content(space-around !important)

.justify-center
  +display(flex)
  +justify-content(center !important)

.align-start
  +display(flex)
  +align-items(flex-start !important)

.align-end
  +display(flex)
  +align-items(flex-end !important)

.align-center
  +display(flex)
  +align-items(center !important)

.align-stretch
  +display(flex)
  +align-items(stretch !important)

.align-baseline
  +display(flex)
  +align-items(baseline !important)

.align-self-start
  +display(flex)
  +align-self(flex-start !important)

.align-self-end
  +display(flex)
  +align-self(flex-end !important)

.align-self-center
  +display(flex)
  +align-self(center !important)

.align-self-stretch
  +display(flex)
  +align-self(stretch !important)

.align-self-baseline
  +display(flex)
  +align-self(baseline !important)

