/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

/// Form controls
/// all manner of input fields and textareas

.private-stepper-input
  display: flex

  .private-stepper-input--input
    padding-right: calc(#{rem-calc(30px)} + #{$input-default-padding-x})

    &:focus + .private-stepper-input--buttons
      z-index: $venus-layer

  .private-stepper-input--buttons
    margin-left: -(rem-calc(31px))
    width: rem-calc(30px)

    > *
      +p-all-0
      +flex-grow(1)
      border-bottom-left-radius: 0
      border-right: 0
      border-top-left-radius: 0

      &:first-child
        border-top: 0
        margin-top: 1px

      &:last-child
        border-bottom: 0
        margin-bottom: 1px

.private-form__control
  +input-metrics($input-default-padding-y - 1, $input-default-padding-x)
  +input-appearance

  +placeholder
    +set-placeholder-style

  &:focus
    +ui-focus

  &[readonly]
    // explicitly set to ="true" to prevent firing on aria-expanded="false" (needed for UIDateInput)
    &:not([aria-expanded='true'])
      // don't apply readOnly styles to UIFloatingForm inputs
      &:not(.private-form__control--inline)
        background-color: $form-control-readonly-background-color
        border-color: $form-control-readonly-border-color
        box-shadow: none

/// Small variant of standard input fields; high-specificity intentional
.private-form__control--small
  &.private-form__control--small
    &.private-form__control--small
      +small-input-appearance

/// error state
// TODO: Why is this !important (this breaks unstyled)
.private-form__control--error,
.private-form__control[aria-invalid='true']
  border-color: $color-candy-apple !important
  box-shadow: 0 0 0 1px $color-candy-apple !important

  &:focus
    box-shadow: 0 0 1px 2px $color-candy-apple !important

.private-form__control-floating__static-input-wrapper
  margin-bottom: 0
  margin-top: rem-calc(8px)

.private-form__control--inline
  +set-font-size($input-sm-font-size)
  background-color: transparent
  border-color: transparent
  border-left-width: 0
  box-shadow: none
  height: rem-calc($input-sm-height)
  padding-bottom: rem-calc(4px)
  padding-left: 0

  &.private-form__control--inline  // extra specificity to override .private-form__control[type=text]
    border-radius: 0

  &.private-button--readonly  // for UISelect (#7804)
    cursor: auto
    user-select: auto

  &[readonly]:not([aria-expanded])
    border-color: transparent

  &.private-form__control--error,
  &.private-form__control[aria-invalid='true']
    border-left-color: transparent !important
    border-right-color: transparent !important
    border-top-color: transparent !important
    box-shadow: none !important

    &:focus
      box-shadow: none !important

.private-form__set--floating--focused
  .private-form__control--inline
    border-bottom-color: $color-calypso
    border-left-color: transparent
    border-radius: 0
    border-right-color: transparent
    border-top-color: transparent
    box-shadow: none

    &.private-button--readonly  // for UISelect (#7804)
      border-bottom: 0

/// @todo it's a bad pattern to remove focus from anything. discussion needed

.private-form__control--unstyled
  background-color: transparent !important
  border-color: transparent !important
  box-shadow: none !important

/// disabled state
.private-form__control--disabled,
.private-form__control:disabled
  background-color: $form-disabled-background-color
  border-color: $form-disabled-background-color
  box-shadow: none !important
  color: $form-disabled-color
  cursor: not-allowed

/// used on dark background
.private-form__control--on-dark
  background-color: $color-olaf

/// buttons styled to look like form controls

.private-button.private-form__control
  padding-bottom: rem-calc(8px)
  padding-right: rem-calc($dropdown-caret-offset-x)
  padding-top: rem-calc(8px)

  &:hover,
  &:focus
    background-color: $color-gypsum

  &.private-button--disabled
    .private-dropdown__caret
      border-top-color: $color-battleship

/// Select box

/// NOTE: `private-form__select` is built with an unstyled <select> within the
/// standard input class (`.private-form__control`) as a wrapper, so we have some
/// magic numbers involved in padding the select box, accounting for the border
/// of the container and the vertical alignment of the text (all px values
/// converted to rem)

/// uses a vendor reset from Filament Group as a base
/// @link https://github.com/filamentgroup/select-css

/// top is padded 2px less
$select-padding: (padding-vertical: $input-default-padding-y - 2)

/// left is default input padding
$select-padding: map-merge($select-padding, (padding-horizontal: $input-default-padding-x))

/// right is padded 32px to account for caret
$select-padding: map-merge($select-padding, (padding-right: 32px))

/// bottom is padded 4px less
$select-padding: map-merge($select-padding, (padding-bottom: $input-default-padding-y - 4))

.private-form__select
  &.private-form__select
    &.private-form__select
      padding: 0

  > select
    +set-border-radius
    +input-metrics($select-padding...)
    color: inherit
    cursor: inherit
    height: 100%

    &:focus
      border-color: rgba($color-focus-ring-base, .5)
      box-shadow: 0 0 4px 1px rgba($color-focus-ring-base, .3)

    &::-moz-focusring
      color: transparent
      text-shadow: 0 0 0 $color-monolith

  &::after
    +make-triangle($color: $dropdown-caret-color, $size: 6px)
    margin-top: rem-calc(-3px)
    right: rem-calc($dropdown-caret-offset-x)

  &.private-form__control--disabled::after
    border-top-color: $color-battleship !important

  &.private-form__control--small > select
    +small-input-appearance
