/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

/// Alerts
/// including notifications: fire alarms, toasts

$illustration-size: 60px
$alert-inline-height: 44px

/// Alert base

.private-alert
  +set-font-size($alert-font-size)
  +align-items(center)
  +justify-content(space-between)
  +display(flex)
  border-style: solid
  border-width: 1px
  min-height: rem-calc($alert-height)
  padding: rem-calc($alert-padding-y) rem-calc($alert-padding-x)
  position: relative
  text-align: left

  &.private-alert--inline
    min-height: rem-calc($alert-inline-height)

  // hack around IE11 flex child not observing min-height
  // by shimming container option with generated content
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
    &::before
      content: ' '
      display: inline-block
      height: rem-calc($alert-height - ($alert-padding-y * 2))
      margin-left: -1px
      vertical-align: middle
      width: 1px

    &.private-alert--inline::before
      height: rem-calc($alert-inline-height - ($alert-padding-y * 2))

    // removes ^ shim for Edge
    @supports (-ms-ime-align: auto)
      &::before
        display: none

  &:not(.private-alert--dismissable)
    .private-alert__body,
    .private-alert__inner
      padding-right: 0

  .private-alert__title
    margin-bottom: rem-calc($alert-padding-y / 2)
    margin-right: rem-calc($alert-padding-x)

  .private-alert__inner
    +align-items(baseline)
    +display(flex)
    +flex-wrap(wrap)
    padding-right: rem-calc($modal-close-button-offset)
    width: 100%

  &.private-alert--banner
    border-color: transparent

/// Alert sub-components

.private-alert__title
  +set-font-family(bold)
  color: inherit

  + .private-alert__body
    margin-top: rem-calc($alert-padding-y / 2)

.private-alert__body
  line-height: 1.4
  padding-right: rem-calc($modal-close-button-offset)

.private-alert__close
  color: $color-eerie
  top: rem-calc(($alert-height / 2) - ($medium-close-button-size / 2))

.private-alert--inline .private-alert__close
    top: rem-calc(($alert-inline-height / 2) - ($medium-close-button-size / 2))

/// Alert variants

/// Danger
.private-alert--danger
  background-color: $color-candy-apple-light
  border-color: $color-candy-apple-medium

  &.private-alert--system-wide
    background-color: $color-lorax-light
    border: 0

/// Warning
.private-alert--warning
  background-color: $color-marigold-light
  border-color: $color-marigold-medium
  color: $base-font-color

  &.private-alert--system-wide
    background-color: $color-alert-warning
    border: 0

/// Success
.private-alert--success
  background-color: $color-oz-light
  border-color: $color-oz-medium

/// Info
.private-alert--info
  background-color: $color-calypso-light
  border-color: $color-calypso-medium

  &.private-alert--system-wide
    border: 0

/// Tip
.private-alert--tip
  background-color: $color-olaf
  border: 1px solid $color-calypso-medium

/// Marketing/Upgrade banners
.private-alert--banner
  +background-image-linear-gradient(89deg, $color-lorax 0%, $color-norman 100%)
  color: $color-olaf

  &:not(.private-alert--dismissable)
    .private-alert__body,
    .private-alert__inner
      padding-right: 0

  .private-alert__title
    +align-self(inherit)
    +flex-grow(1)

  .private-alert__body
    +flex-grow(9999)
    margin-top: 0

  .private-alert__close
    color: $color-olaf

.private-floating-alert
  box-shadow: 0 1px 4px 2px rgba($color-heffalump, .06)
  display: inline-flex
  max-width: 668px
  pointer-events: auto

  &.private-alert--inline
    box-shadow: 0 1px 4px 2px rgba($color-heffalump, .06)

@media only screen and (max-width: rem-calc(map-get($grid-breakpoints, md)))
  .private-floating-alert
    max-width: calc(100% - 50px)

.private-floating-alert-list
  display: block
  left: 0
  pointer-events: none
  position: fixed
  text-align: center
  // Overridden dynamically by the component
  top: 0
  width: 100%
  z-index: $uranus-layer

  &.private-floating-alert-list--contextual
    position: relative
    top: 0

    .private-floating-alert
      max-width: calc(100% - 50px)

  .private-floating-alert-list__child-wrapper
    left: 0
    overflow: auto
    position: absolute
    top: 0
    width: 100%

  .private-floating-alert-list__child + .private-floating-alert-list__child
    margin-top: rem-calc(16px)

