/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

$overlay-blur-transition: filter steps(5, end) $hoverlay-transition-timing

/// Overlays
/// Note: the filter approach is pretty expensive w/rt rendering. might need/// to jettison it.

// sass-lint:disable no-qualifying-elements
// sass-lint:disable class-name-format
body.uiFullScreenBackground > .page,
body.uiFullScreenBackground > .app
  filter: blur(5px)

.private-overlay,
.private-hoverlay__overlay
  background-color: $overlay-background-color-translucent
  bottom: 0
  contain: content
  left: 0
  overflow: auto
  position: fixed
  right: 0
  top: 0
  z-index: $jupiter-layer

  &.private-overlay--contextual
    contain: none
    position: absolute

    .private-modal__container
      height: 100%

  // Prevent scrollbar from appearing during slidein-from-right transitions
  @media only screen and (min-width: rem-calc(800px))
    overflow-x: hidden

  // Add whitespace above and below the modal, except on small screens
  @media only screen and (min-width: rem-calc(map-get($grid-breakpoints, sm) + 1))
    padding-bottom: rem-calc($modal-overlay-padding-y)
    padding-top: rem-calc($modal-overlay-padding-y)

.private-overlay--light
  background-color: rgba($color-olaf, .79)

.private-overlay--contextual
  position: absolute

// account for shadow (#1772)

.private-overlay__overlay-content,
.private-overlay__overlay-content > *
  height: 100%
  margin: 0
  width: 100%

.private-hoverlay
  overflow: hidden
  position: relative

  &.private-hoverlay--full-card
    .private-hoverlay__child
      margin-bottom: $card-shadow-blur-radius // #1772

    .private-hoverlay__overlay
      +set-border-radius
      bottom: $card-shadow-blur-radius  // #1772

  &.private-hoverlay--bottom-half-card
    .private-hoverlay__overlay
      border-bottom-left-radius: rem-calc($button-radius)
      border-bottom-right-radius: rem-calc($button-radius)

  .private-hoverlay__overlay
    +transition(background-color $hoverlay-transition-timing)
    background-color: rgba($color-slinky, 0)
    z-index: $mercury-layer

    &.private-overlay--contextual
      padding: 0

    &.private-hoverlay__overlay--clickable
      cursor: pointer
      outline: none

    + .private-hoverlay__children
      filter: blur(0)
      transition: $overlay-blur-transition

  .private-overlay__overlay-content
    +transition(opacity $hoverlay-transition-timing)
    color: $color-olaf
    opacity: 0
    outline: none

  a:focus > .private-hoverlay > .private-hoverlay__overlay,
  .private-hoverlay__button:focus  > .private-hoverlay > .private-hoverlay__overlay,
  .private-hoverlay__overlay--hovered,
  .private-hoverlay__overlay:focus,
  .private-hoverlay__overlay--focused
    +transition(background-color $hoverlay-transition-timing)
    background-color: rgba($color-slinky, $hoverlay-opacity)

    .private-overlay__overlay-content
      +transition(opacity $hoverlay-transition-timing)
      +user-select(none)
      opacity: 1

    + .private-hoverlay__children--blur
      filter: blur(5px)
      transition: $overlay-blur-transition

.private-avatar-hoverlay
  border-radius: 100%

  .private-hoverlay__overlay
    border-radius: 100%

  .private-avatar-hoverlay__foreground-text
    +set-heading(heading-7)
    +m-bottom-0
    text-align: center
    text-decoration: underline

.private-overlay-highlight
  display: inline-block
  .private-overlay-highlight__content-shadow
    border-radius: 40px
    box-shadow: 0 0 100px 5253px $overlay-background-color-translucent
    display: inline-block
    position: relative
    z-index: $jupiter-layer

  .private-overlay-highlight__content
    position: relative
    z-index: $saturn-layer

