/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

.private-button
  +button-borders
  +font-smoothing(auto)
  +set-font-family(regular)
  +text-center
  +user-select(none)
  +ui-transition
  display: inline-block
  max-width: 100%
  overflow: hidden
  text-overflow: ellipsis
  vertical-align: $input-vertical-alignment
  white-space: nowrap

  &:hover
    text-decoration: none

  + .private-button
    /// space adjacent buttons horizontally if above `sm` breakpoint
    @media only screen and (min-width: rem-calc(map-get($grid-breakpoints, sm) + 1))
      margin-left: rem-calc($base-spacing-x)

    /// space adjacent buttons vertically if at or below `sm` breakpoint
    @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
      margin-top: rem-calc($base-spacing-y)

  /// handles the case of form field styling applied to a button

  &.private-form__control
    +set-font-family(regular)

    &.private-button--active:not(.private-button--readonly)
      +ui-focus($important: true)

  /// this is the best. it solves the problem of the multiple disabled button
  /// style by having only one. easily worth the price of admission alone to
  /// Space Sword

  &[aria-disabled='true'],
  &.private-button--disabled
    +set-disabled-style

  // Default behavior w/ useFlex "un-centers" text if too short to be truncated (#6059)
  .private-truncated-string--is-flex
    justify-content: center
  &.private-button--readonly
    &:not(.private-form__control--inline)
      +set-readonly-style
      -ms-user-select: auto
      user-select: auto
      // Safari's selector is more specific to avoid showing padding in selection (#8751)
      .private-typeahead-result
        -webkit-user-select: auto

.private-button--block
  width: 100%

@mixin button-metrics($font-size, $padding-y, $padding-x)
  font-size: $font-size
  line-height: $font-size + 2px
  padding: ($padding-y - 1px) $padding-x

  @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
    width: 100%

/// sizes
.private-button--xs
  +button-metrics($button-xs-font-size, $button-xs-padding-y, $button-xs-padding-x)

.private-button--sm
  +button-metrics($button-sm-font-size, $button-sm-padding-y, $button-sm-padding-x)

.private-button--toolbar
  padding-left: rem-calc(6px)
  padding-right: rem-calc(6px)

.private-button--default
  +button-metrics($button-default-font-size, $button-default-padding-y, $button-default-padding-x)
  +set-font-family(medium)

.private-button--lg
  +button-metrics($button-lg-font-size, $button-lg-padding-y, $button-lg-padding-x)


/// color variants

.private-button--primary
  background-color: $color-lorax
  border-color: $color-lorax
  color: $color-olaf

  &:hover
    background-color: $color-button-primary-hover-fill
    border-color: $color-button-primary-hover-fill
    color: $color-olaf

  &.private-button--active
    background-color: $color-lorax-dark
    border-color: $color-lorax-dark
    color: $color-olaf

.private-button--primary--white
  background-color: $color-olaf
  border-color: transparent
  color: $color-obsidian

  &:hover
    background-color: rgba($color-olaf, .9)
    border-color: transparent
    color: $color-obsidian

  &.private-button--active
    background-color: rgba($color-olaf, .8)
    border-color: transparent
    color: $color-obsidian

.private-button--secondary
  background-color: $color-olaf
  border-color: $color-lorax
  color: $color-button-secondary-text

  &:hover
    background-color: $color-button-secondary-hover-fill
    color: $color-button-secondary-text

  &.private-button--active
    background-color: $color-button-secondary-active-fill
    color: $color-button-secondary-text

.private-button--secondary--ghost
  background-color: transparent
  border-color: $color-olaf
  color: $color-olaf

  &:hover
    background-color: rgba($color-olaf, .05)
    border-color: $color-olaf
    color: $color-olaf

  &.private-button--active
    background-color: rgba($color-olaf, .1)
    border-color: $color-olaf
    color: $color-olaf

.private-button--tertiary
  background-color: $color-heffalump
  border-color: $color-heffalump
  color: $color-olaf

  &:hover
    background-color: $color-slinky
    border-color: $color-slinky
    color: $color-olaf

  &.private-button--active
    background-color: $color-obsidian
    border-color: $color-obsidian
    color: $color-olaf

.private-button--tertiary-light
  background-color: $color-koala
  border-color: $color-battleship
  color: $color-button-tertiary-light-text

  &:hover
    background-color: $color-gypsum
    color: $color-button-tertiary-light-text

  &.private-button--active
    background-color: $color-battleship
    border-color: $color-eerie
    color: $color-button-tertiary-light-text

.private-button--tertiary-extra-light
  background-color: $color-olaf
  border-color: $color-battleship
  color: $color-button-tertiary-light-text

  &:hover
    background-color: $color-gypsum
    color: $color-button-tertiary-light-text

  &.private-button--active
    background-color: $color-battleship
    border-color: $color-eerie
    color: $color-button-tertiary-light-text

.private-button--destructive
  background-color: $color-candy-apple
  border-color: $color-candy-apple
  color: $color-olaf

  &:hover
    background-color: $color-button-danger-hover-fill
    border-color: $color-button-danger-hover-fill
    color: $color-olaf

  &.private-button--active
    background-color: $color-candy-apple-dark
    border-color: $color-candy-apple-dark
    color: $color-olaf

.private-button--transparent
  +set-transparent
  +link-style
  +user-select(inherit)

  &.private-button--xs
    padding-left: rem-calc($button-xs-padding-x / 2)
    padding-right: rem-calc($button-xs-padding-x / 2)

  &.private-button--sm
    padding-left: rem-calc($button-sm-padding-x / 2)
    padding-right: rem-calc($button-sm-padding-x / 2)

  &.private-button--default
    padding-left: rem-calc($button-default-padding-x / 2)
    padding-right: rem-calc($button-default-padding-x / 2)

.private-button--form
  &:hover
    color: $base-font-color

.private-button--form.private-button--form--on-dark,  // extra specificity to override .private-form-control
.private-button--form.private-button--form--on-dark.private-button--form--on-dark:hover,  // even more specificity needed
.private-button--form.private-button--form--on-dark.private-button--form--on-dark:focus   // even more specificity needed
  background-color: $color-olaf

.private-button__link
  +set-transparent
  +link-style
  +user-select(inherit)
  border: 0
  display: inline
  font-size: inherit
  line-height: inherit
  overflow: visible
  padding: 0
  text-align: inherit
  vertical-align: inherit
  white-space: normal

  .private-icon
    margin-right: rem-calc($base-icon-spacing-x, 'em')

.private-button__link,
.private-button--transparent
  &.private-button--disabled,
  &.private-button--readonly.private-button--readonly
    background-color: transparent !important
    border-color: transparent !important
    text-decoration: none !important

  &:not(.private-button--disabled)
    .private-icon
      +icon-within-link

.private-button__link--danger
  +link-style-danger

  &.private-button--active
    +link-style-danger($color: $color-candy-apple-dark)

.private-button__link--on-dark
  +link-style-on-dark

  &.private-button--active
    +link-style-on-dark($color: $dark-link-active-color)

.private-button__link--on-bright
  +link-style-on-bright

.private-tag
  .private-button__link
    color: inherit

/// meant to reset default button styling in order to
/// take advantage of the full range of accessibility
/// and browser behaviors afforded by the <button> element
/// ------------------------------------------------------
/// http://www.karlgroves.com/2013/05/14/links-are-not-buttons-neither-are-divs-and-spans/

.private-button--unstyled
  background: none
  border: 0
  color: inherit
  padding: 0
  text-align: inherit
  text-transform: inherit

  + .private-button--unstyled
    margin-left: rem-calc($base-spacing-x)

// sass-lint:disable no-qualifying-elements
button.private-button--unstyled,
input.private-button--unstyled[type='button'],
input.private-button--unstyled[type='submit']
  &::-moz-focus-inner
    border: 0
    padding: 0
// sass-lint:enable no-qualifying-elements

/// account for icons inside of buttons

.private-button--non-link,
.private-button--internal-spacing

  /// module spacing trickery
  /// accounts for spacing of child elements within buttons
  > *:not(.sr-only) + *
    margin-left: rem-calc($base-icon-spacing-x, em)

  /// this is unfortunate, but it stops icons from blowing up size of buttons
  .private-icon:not(.private-icon-circle__inner)
    line-height: 0

.private-button--non-responsive
  width: auto

  // always space adjacent buttons horizontally when non-responsive
  + .private-button--non-responsive
    margin-left: rem-calc($base-spacing-x)
    margin-top: 0

/// UIIconButton
.private-button--icon-only
  &.private-button__link,
  &.private-button--transparent
    .private-icon:not(.private-icon-circle__inner)
      color: inherit

.private-button--circle
  border-radius: 100%

// Loading Button

.private-loading-button
  position: relative

  .private-loading-button__spinner
    left: 50%
    margin: 0
    opacity: 0
    position: absolute
    top: 50%
    transform: translate(-50%, -50%) scale(.7)
    transition: opacity .2s cubic-bezier(.42, 0, .58, 1)

  .private-loading-button__content
    opacity: 1
    transition: opacity .2s cubic-bezier(.42, 0, .58, 1)

  &.private-loading-button--loading
    &:not(.private-button--secondary--ghost)
      background-color: $color-olaf

    .private-loading-button__spinner
      opacity: 1

    .private-loading-button__content
      opacity: 0
