/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

.private-step-indicator
  +display(inline-flex)
  +flex(1 1 auto)
  +flex-wrap(wrap)
  // alignment to match previous version
  margin-left: rem-calc(2px)
  margin-top: rem-calc(2px)

/// Subcomponents

.private-step-indicator__section
  +display(flex)
  +flex(1 1 0%)
  +justify-content(center)
  color: $connected-step-unselected-color

  // sass-lint:disable nesting-depth
  &:first-of-type
    .private-step-indicator__line
      &::before
        visibility: hidden

  &:last-of-type
    .private-step-indicator__line
      &::after
        visibility: hidden
  // sass-lint:enable nesting-depth

  &:focus
    overflow: hidden  /// prevents oddly shaped focus ring

.private-step-indicator--horizontal
  +align-items(flex-start)
  +justify-content(space-between)

  .private-step-indicator__section
    +align-items(center)
    +flex-direction(column)
    +justify-content(center)

  .private-step-indicator__line
    &::before,
    &::after
      +flex(1 1 100%)
      display: inline-block

.private-step-indicator__section--active,
.private-step-indicator__section--complete
  color: $connected-step-selected-color

.private-step-indicator--customized
  .private-step-indicator__point
    transform: none

  // for customized, vertical indicators, we use bold text labels
  &.private-step-indicator--vertical
    .private-step-indicator__section--active
      .private-step-indicator__text
        +set-font-family(demibold)

.private-step-indicator__section--active
  .private-step-indicator__line
    &::after
      color: $connected-step-unselected-color

.private-step-indicator__line
  +align-items(center)
  +display(flex)
  +justify-content(center)
  width: 100%

  &::before,
  &::after
    border-top: 2px solid currentColor
    content: ' '
    position: relative
    width: 100%

.private-step-indicator__item
  +set-font-family(regular)
  +set-font-size(12px)
  color: $base-font-color
  text-align: center
  text-transform: uppercase
  word-wrap: break-word

  // unfortunate we had to get this specific with this override, but it was needed
  // to get this change in: https://git.hubteam.com/HubSpot/UIComponents/pull/2343
  small,
  .private-microcopy
    +font-smoothing(auto)
    +set-font-family(regular)
    text-transform: none

.private-step-indicator__point
  +flex(0 0 auto)
  line-height: 0
  padding: rem-calc(4px) 0
  transition: opacity $micro-transition-timing ease-out

  > *
    line-height: normal
    vertical-align: top

.private-step-indicator__clickable
  cursor: pointer

  &:hover
    .private-step-indicator__point
      opacity: .65

.private-step-indicator__status
  +flex-basis(100%)
  display: none
  text-align: center

  &::after
    content: '\200B'

/// Compact version
.private-step-indicator--compact
  .private-step-indicator__text
    display: none

  .private-step-indicator__status
    display: block

/// Flush version: removes horizontal padding
.private-step-indicator--flush
  .private-step-indicator__section
    // sass-lint:disable nesting-depth
    .private-step-indicator__line
      &::before
        display: none

    &:last-of-type
      +flex(0 0 auto)
      .private-step-indicator__line
        &::after
          display: none
    // sass-lint:enable nesting-depth

/// Vertical orientation

.private-step-indicator--vertical
  +align-self(stretch)
  +flex-direction(column)

  .private-step-indicator__item
    flex-grow: 1
    padding-bottom: rem-calc(28px)
    text-align: left

  .private-step-indicator__section
    +align-items(flex-start)
    +justify-content(flex-start)
    +flex-basis(auto)
    position: relative

    // IE11 fix for jumping on hover
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
      border-top: 1px solid transparent

    // resets ^ for Edge
    @supports (-ms-ime-align: auto)
      border-top: 0

  .private-step-indicator__point
    margin-right: 0
    padding-bottom: 0
    padding-top: 0

  .private-step-indicator__line
    +align-items(center)
    +display(flex)
    +flex-direction(column)
    +justify-content(flex-start)
    height: 100%
    left: 0
    position: absolute
    top: 0
    width: auto

    &::after
      +flex(1 0 auto)
      border-left: 2px solid currentColor
      border-top: 0
      content: ' '
      display: inline-block
      position: relative
      width: auto

    &::before
      display: none

  .private-step-indicator__status
    display: none

/// exception for title presentation in wizard step header
.private-wizard__header-with-overview
  .private-step-indicator__item
    +font-smoothing
    +set-font-family(demibold)
    +set-font-size(11px)
