/* @bend-expect trellis-tokenized */

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Resets
/// Going above and beyond of normalize

*, *::before, *::after
  box-sizing: border-box

/// Standardize rem units across browsers

html
  font-size: $trellis-typography-body-200-font-size

/// Base link styling
a
  +link-style
  cursor: pointer

  .is--code, code
    font-weight: $trellis-typography-body-125-font-weight

/// handle bold text previously affected by normalize

b, strong
  font-weight: $trellis-typography-body-125-font-weight

// set uni-directional margins

// sass-lint:disable space-after-comma
ul, ol ,dd,
p, figure, figcaption,
pre, table, fieldset, hr
  +single-direction-module-spacing
// sass-lint:enable space-after-comma

// Don't add whitespace below nested lists

li > ol, li > ul
  margin-bottom: $trellis-space-0

/// override FIGURE here to keep normalize file pristine

figure
  margin: $trellis-space-0

// Cancel out special styling for invalid elements in Firefox (#5528)

*:-moz-ui-invalid
  box-shadow: none

// Don't show pointer cursor on elements with aria-disabled={true}

[aria-disabled='true']
  cursor: inherit

// Don't show focus outline on descendants of .hubspot-disable-focus-styles,
// except for inputs, textareas, and anything with .keep-default-focus-styles.

.hubspot-disable-focus-styles
  *:focus:not(input):not(textarea):not(.keep-default-focus-styles)
    outline: $trellis-border-width-0

// Fix iOS input rounding
// sass-lint:disable no-qualifying-elements
input[type='text'],
input[type='search'],
input[type='button'],
input[type='submit']
  -webkit-appearance: none

// Fix inputs/textareas inheriting hyphenation
input,
textarea
  -webkit-hyphens: none
  hyphens: none
