/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

@import HubStyle/stylesheets/hubstyle/base

.private-expandable-text
  overflow: hidden

.private-expandable-text__container
  +ui-transition
  +display(flex)
  +flex-direction(column)

.private-expandable-text__overhang-wrapper
  position: relative

.private-expandable-text--scrollable
  border: 1px solid $color-battleship
  overflow-x: auto
  overflow-y: scroll
  padding: rem-calc(16px) rem-calc(20px)

.private-expandable-text__toggle-button
  +align-self(center)
  margin: rem-calc(8px) 0

  &.align-left
    +align-self(flex-start)

  &.align-right
    +align-self(flex-end)
