@import HubStyle/stylesheets/hubstyle/base

$button-group-item-background-color: #f5f8fa
$button-group-item-border-color: #cbd6e2
$button-group-item-color: #516f90
$button-group-item-hover-background-color: #fff
$button-group-item-selected-background-color: #e5eaf0

// sass-lint:disable nesting-depth
.private-button__group

  &.private-button__group--horizontal
    display: inline-block

    > *
      margin-top: 0
      position: relative

      &:not(.hidden)
         display: inline-block !important

      &:first-child
        border-bottom-right-radius: 0
        border-top-right-radius: 0

      &:last-child
        border-bottom-left-radius: 0
        border-top-left-radius: 0

      &:not(:first-child)
        margin-left: -1px !important // no gaps are by design

  &.private-button__group--vertical
    display: inline-flex
    flex-direction: column

    > *
      display: block
      margin-left: 0
      position: relative

      &:first-child
        border-bottom-left-radius: 0
        border-bottom-right-radius: 0

      &:last-child
        border-top-left-radius: 0
        border-top-right-radius: 0

      &:not(:first-child)
        margin-top: -1px !important // no gaps are by design

  > *
    &:not(:first-child):not(:last-child)
      border-radius: 0

    // extra specificity needed to override previous first-child, last-child rules
    &:only-child:only-child
      +set-border-radius
      border-width: $button-border-width

    &.private-button--active:not([aria-disabled='true'])
      z-index: $mercury-layer

    &[aria-disabled='true'],
    &.private-button--disabled
      border-color: $color-battleship !important
  // sass-lint:enable nesting-depth

.hubspot-enable-focus-styles .private-button__group
  > *:focus
    z-index: $venus-layer

.private-button-select-group__option
  background-color: $button-group-item-background-color
  border-color: $button-group-item-border-color !important
  color: $button-group-item-color

  &:hover
    background-color: $button-group-item-hover-background-color
    color: $button-group-item-color

  &.private-button--active
    background-color: $button-group-item-selected-background-color !important
    color: $button-group-item-color
