@import HubStyle/stylesheets/hubstyle/base

/// Static labels
/// Similar to Bootstrap's `.add-on` for making a compound input with a
/// prefix or suffix label

.private-input-static-label__label
  +input-metrics($input-default-padding-y - 1, 0)
  +set-font-size($input-font-size)
  +user-select(none)
  cursor: default
  height: rem-calc($input-default-height)

.private-input-static-label--small
  .private-input-static-label__label
    +input-metrics(4px, 0)
    +set-font-size(14px)
    height: rem-calc($input-sm-height)
