@import HubStyle/stylesheets/hubstyle/base

// /UIMoveableGroup


$close-button-offset-x: $button-lg-width
$grippie-offset-x: 26px

.private-moveable-group
  padding-right: rem-calc($close-button-offset-x)
  position: relative

  // spacing between siblings
  + .private-moveable-group
    margin-top: rem-calc($form-set-vertical-separation)

  // padding for grippie
  .private-form__set:first-of-type
    .private-form__control
      padding-left: rem-calc($grippie-offset-x)

  // tweaks to form__set
  .private-form__set
    width: 100%

    + .private-form__set
      margin-top: 0

.private-movable-group__grippie
  height: rem-calc($input-default-height)
  left: 0
  position: absolute
  top: 0
  width: rem-calc($grippie-offset-x)


.private-moveable-group__inner
  +display(flex)

.private-moveable-group__close
  position: absolute
  right: 0
  top: 0
  width: rem-calc($close-button-offset-x)

