/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

.private-slider
  +display(flex)
  +flex-direction(row)
  cursor: pointer
  height: rem-calc(20px)

.private-slider__number-input
  width: 25%

.private-slider__slider
  +flex-grow(1)

.private-slider__slider-track
  position: relative

.private-slider__control
  +set-grab-cursor
  background-color: $color-olaf
  border-radius: 50%
  box-shadow: 0 0 0 1px rgba($color-slinky, .3), 0 1px 24px 0 rgba($color-monolith, .08)
  height: rem-calc($slider-control-size)
  position: relative
  top: rem-calc(-4px)
  transform: translateX(rem-calc(-$slider-control-size / 2))
  width: rem-calc($slider-control-size)

.private-slider--dragging,
.private-slider--dragging .private-slider__control
  +set-grabbing-cursor

// sass-lint:disable class-name-format
.private-slider__input > .uiNumberInput
  margin-bottom: rem-calc(20px)
// sass-lint:enable class-name-format

// min/max labels
.private-slider__label
  +flex-shrink(0)
  +user-select(none)
  position: relative
  top: rem-calc(-2px)

.private-slider__label--min
  padding-right: rem-calc(15px)

.private-slider__label--max
  padding-left: rem-calc(15px)

// tickmarks
.private-slider__tickmarks
  +display(flex)
  +flex-direction(row)
  +justify-content(space-between)
  position: absolute
  width: 100%

.private-slider__tickmark
  background-color: $color-battleship
  height: rem-calc(14px)
  width: rem-calc(2px)

  &.private-slider__tickmark--filled
    background-color: $color-calypso

// large variation
.private-slider--large
  .private-slider__control,
  .private-slider__label--min,
  .private-slider__label--max
    top: 0

  .private-slider__tickmark
    height: rem-calc(20px)
    width: rem-calc(3px)

.private-slider--disabled,
.private-slider--disabled .private-slider__control
  cursor: not-allowed

  .private-slider__tickmark--left
    background-color: $color-koala
    border-color: $color-koala

// Read-only styles
.private-slider--readonly
  &, & .private-slider__control
    cursor: initial
  .private-slider__control
    background: $color-koala
    box-shadow: 0 0 0 1px rgba($color-flint, .3), 0 1px 24px 0 rgba($color-monolith, .08)
  .private-slider__tickmark.private-slider__tickmark--filled
    background-color: $color-koala

// class to show drag cursor everywhere while slider is being dragged

.hubspot-drag-cursor
  +set-grabbing-cursor
