/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

// sass-lint:disable no-mergeable-selectors
.private-table--condensed
  .private-table-sort-toggle
    margin-bottom: rem-calc($table-compact-padding-y * -1)
    margin-top: rem-calc($table-compact-padding-y * -1)
    padding-bottom: rem-calc($table-compact-padding-y)
    padding-top: rem-calc($table-compact-padding-y)
// sass-lint:enable no-mergeable-selectors

.private-table-sort-toggle__inner
  +align-items(center)
  +display(inline-flex)
  text-align: inherit

// Ensure that the draghandle doesn't affect size/positioning
.private-table-sort-toggle__draghandle
  left: 0
  position: absolute
  top: 0

.private-table-sort-toggle__icon
  +flex-shrink(0)
  display: inline-block
  fill: $table-sort-arrow-color
  margin-left: rem-calc(6px)
  margin-right: 0
  width: rem-calc(7.5px)

  // hack for IE11 bug where icon grows to enormous height
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
    height: rem-calc(16px)

  // resets ^ for Edge
  @supports (-ms-ime-align: auto)
    height: auto

.private-table-sort-toggle--sorted
  background-color: $table-sortable-sorted-background-color

.private-table-sort-toggle--ascending
  .private-table-sort-toggle__icon__upper
    fill: $table-sort-arrow-selected-color

.private-table-sort-toggle--descending
  .private-table-sort-toggle__icon__lower
    fill: $table-sort-arrow-selected-color

.private-table-sort-toggle--align-right
  .private-table-sort-toggle__inner,
  .private-table-sort-toggle__content
    // No single selector works in Firefox + other browsers
    margin-left: auto
    margin-right: rem-calc(6px)

