/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

$search-padding-right: 44px

=clear-button-appearance($size: $input-font-size)
  font-size: rem-calc($size)
  line-height: rem-calc($size)
  padding-bottom: rem-calc($size)
  padding-left: rem-calc($size)
  padding-right: rem-calc($size)
  padding-top: rem-calc($size * 3/4)

/// Search box

.private-search-control
  display: inline-block
  position: relative
  vertical-align: $input-vertical-alignment
  width: 100%

.private-search-control__input
  padding-right: rem-calc($search-padding-right)

  &::-ms-clear
    display: none

  &:focus + .private-search-control__foreground
    z-index: $venus-layer

.private-search-control__foreground
  bottom: $input-border-width
  pointer-events: none
  position: absolute
  right: 0
  top: $input-border-width

.private-search-control__help-text
  color: $color-eerie
  white-space: nowrap

.private-search-control__help-text--hidden
  visibility: hidden

.private-search-control__icon
  +search-icon-appearance
  margin-top: 11px

.private-search-control__icon--small
  +search-icon-appearance($size: $input-sm-font-size)

.private-search-control__icon--hidden
  visibility: hidden

// Clear button mimicks +search-icon-appearance, but with padding instead of margin
.private-search-control__clear-button
  +clear-button-appearance
  color: $icon-default-color
  position: absolute
  right: 1px
  top: 1px

.private-search-control__clear-button--small
  +clear-button-appearance($size: $input-sm-font-size)

.private-search-control--xl
  .private-search-control__input
    +set-font-size(20px)
    +input-metrics($input-default-padding-y * 2, $input-default-padding-x * 2, $padding-right: 72px)
    height: 60px
    line-height: normal
    padding-right: rem-calc($search-padding-right * 2)

  .private-search-control__icon
    font-size: 20px
    line-height: 20px
    margin-left: 20px
    margin-right: $input-default-padding-x * 2
    margin-top: 17px

  .private-search-control__clear-button
    font-size: 20px
    padding-right: $input-default-padding-x * 2
    padding-top: 20px

// Read-only styles and updated style fixes for disabled
.private-search-control__input[readonly],
.private-search-control__input[disabled]
  & + .private-search-control__foreground
    .private-search-control__icon,
    .private-search-control__icon--small
      color: $color-flint
