@import HubStyle/stylesheets/hubstyle/base

/// Images

.private-image
  vertical-align: middle

.private-image--responsive
  display: block
  height: auto
  max-width: 100%

.private-image--circle
  border-radius: 100%

.private-image--rounded,
.private-image--thumbnail
  +set-border-radius

.private-illustration
  max-width: 100%

  &.private-illustration--disabled
    opacity: .5

.private-image--bordered
  border: 1px solid currentColor

.private-image--object-fit-contain
  object-fit: contain

.private-image--object-fit-cover
  object-fit: cover

.private-image--fitted
  height: 100%
  width: 100%
