/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Standalone back button

// sass-lint:disable nesting-depth
.private-back-button
  &.private-back-button
    &.private-back-button
      +flex-shrink(0)
      display: inline-block
      margin: 0
      padding: 0

      > *
        font-size: inherit
        vertical-align: inherit

// Contextual styling within editor UIToolBar
.private-tool-bar--dark
  .private-back-button
    &.private-back-button
      &.private-back-button
        vertical-align: bottom

    &.private-back-button > *
      +link-style-on-dark
      text-decoration: none
      vertical-align: inherit

  // Overrides for UIBreadcrumbs
  .private-breadcrumbs__arrow--back
    &.private-breadcrumbs__arrow--back
      &.private-breadcrumbs__arrow--back
        &.private-breadcrumbs__arrow--back
          +set-font-size($back-button-arrow-size)
          margin-right: 0
// sass-lint:enable nesting-depth

