/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Link variants

.private-link--on-dark
  +link-style-on-dark

.private-link--on-bright
  +link-style-on-bright

.private-link--underlined
  text-decoration: underline

.private-link--danger
  +link-style-danger

.private-link--mention
  +link-style-mention

.private-link--disabled
  color: $color-button-disabled-text !important
  cursor: not-allowed
  text-decoration: none !important

.private-link--unstyled, .private-link--unstyled:hover
  +font-smoothing(auto)
  color: inherit
  font: inherit
  line-height: inherit
  text-decoration: inherit

.private-link .private-icon:not(.private-icon-circle__inner)
  +icon-within-link
  margin-right: rem-calc($base-icon-spacing-x, em)

.private-link--on-dark .private-icon:not(.private-icon-circle__inner)
  +icon-within-link($dark-link-icon-color)

.private-link--on-bright .private-icon:not(.private-icon-circle__inner)
  +icon-within-link($bright-link-icon-color)

.private-link--disabled .private-icon:not(.private-icon-circle__inner)
  +icon-within-link(inherit)

.private-link--external__icon.private-link--external__icon.private-link--external__icon
  margin-left: rem-calc($inline-icon-spacing-x, em)
  margin-right: rem-calc($inline-icon-spacing-x, em)
