/* @bend-expect trellis-tokenized */

@import sassPrefix/hyphens
// IE8+

.truncate-text
  +hyphens(none)
  overflow: hidden !important
  text-overflow: ellipsis !important
  white-space: nowrap !important
  width: 100% !important

.truncate-text-weak
  +hyphens(none)
  overflow: hidden !important
  text-overflow: ellipsis !important
  white-space: nowrap !important
