@import HubStyle/stylesheets/hubstyle/base

/// Multi-selectable input
/// Styling for react-select with Space Sword theme applied
/// Mostly brute-forces styles on existing hooks from <Select />

// sass-lint:disable class-name-format

// Extra specificity here to help override React Select styles (HubSpot/UIComponents#1607)
.private-form__selectplus
  +set-border-radius
  cursor: pointer
  width: 100%

  .Select-control
    +input-appearance
    +display(flex)
    +align-items(center)
    +user-select(none)
    cursor: inherit
    height: auto
    min-height: rem-calc($input-default-height)


  &:not(.private-form__selectplus--fixed-height)
    .Select-control
      height: auto

  &.is-open
    cursor: text

  .Select-placeholder,
  &:not(.Select--multi) > .Select-control .Select-value
    +input-metrics($input-default-padding-y - 1, $input-default-padding-x)
    color: inherit
    line-height: inherit
    right: rem-calc(32px)

    .private-dropdown__item__decoration
      +m-x-1
      left: 0

  &.Select--single
    .Select-value
      position: relative

    // Prevent tag from affecting button height
    .private-tag
      cursor: inherit
      margin-bottom: -1px
      margin-top: -1px

    // Hide keyboard cursor when it would overlap with the single-select value
    .Select-value + .Select-input
      left: 0
      opacity: 0
      position: absolute
      top: 0

    // Hack to prevent mismatched cursor in read-only mode
    .uiTypeaheadResults__item
      cursor: inherit

  &.Select--multi
    .private-tag
      +display(inline-flex)
      +flex-shrink(0)
      margin-left: rem-calc(8px)
      margin-top: rem-calc(8px)
      max-width: calc(100% - #{rem-calc(16px)})
      vertical-align: top

      &:last-of-type
        margin-bottom: rem-calc(8px)  // HubSpot/UIComponents#1643

    .Select-arrow-zone,
    .Select-clear-zone
      display: table-cell
      margin-left: rem-calc(5px)
      width: rem-calc(28px)

  .Select-multi-value-wrapper
    margin-bottom: -1px
    margin-top: -1px
    min-height: rem-calc($input-default-height)
    width: calc(100% - #{rem-calc(32px)})

  .Select-input
    +flex(1)
    height: auto

    > input
      +input-metrics($input-default-padding-y - 1, $input-default-padding-x)
      cursor: text
      height: auto
      line-height: rem-calc(22px)
      padding-left: 0

      &:-moz-focusring
        outline: 0

  .Select-clear-zone
    &:hover
      color: inherit

  .Select-clear
    line-height: inherit

  .Select-arrow-zone,
  .Select-clear-zone
    padding-right: rem-calc($dropdown-caret-offset-x)
    width: auto

  .Select-arrow-zone > .Select-arrow  // yep, we need the extra specificity: #2039
    +make-triangle($color: $dropdown-caret-color, $size: 6px)
    top: 0

  // Make the input act like part of the button when the dropdown is closed
  .Select:not(.is-open)
    .Select-input
      cursor: pointer
      pointer-events: none

  &.is-focused:not(.is-open) > .Select-control
    +ui-focus

  &.has-value:not(.Select--multi) > .Select-control .Select-value .Select-value-label,
  &.has-value.is-pseudo-focused:not(.Select--multi) > .Select-control .Select-value .Select-value-label
    color: $base-font-color

  &.is-disabled
    &:not(.is-readonly)
      > .Select-control
        +set-disabled-style

        // sass-lint:disable nesting-depth
        .Select-arrow-zone > .Select-arrow
          border-top-color: $color-battleship

        .Select-value-label
          color: $color-button-disabled-text !important
        // sass-lint:enable nesting-depth

  &.is-readonly > .Select-control
    +set-readonly-style

    .Select-arrow-zone > .Select-arrow
      display: none

/// Results styles
.private-dropdown, .private-dropdown__results
  border-radius: inherit
  .Select
    border-radius: inherit

  .Select-menu
    border-bottom-left-radius: inherit
    border-bottom-right-radius: inherit
    max-height: rem-calc($dropdown-menu-height + $dropdown-padding-y)
    padding-bottom: rem-calc($dropdown-padding-y)
    padding-top: rem-calc($dropdown-padding-y)

  .Select-menu-outer
    border: 0
    border-radius: inherit
    box-shadow: none
    margin-top: 0
    max-height: none
    position: relative

  .Select-option
    padding: 0

    button
      +dropdown-item-appearance

    .private-checkbox
      min-height: $dropdown-item-default-height

    &:last-child
      border-radius: 0 // override rounded corners from react-select

    &.is-selected
      background-color: inherit
      color: inherit

    &.is-focused
      +dropdown-item-hover
      color: inherit

    &.is-disabled

      button
        +set-disabled-style
        background-color: $color-olaf !important

  .Select-option-group-label
    +dropdown-group-label
    color: inherit
    padding: rem-calc(4px) 0 0

  .Select-noresults
    // Same styles as .private-typeahead-result--disabled
    +set-disabled-style
    background-color: $color-olaf !important
    padding: rem-calc(8px) rem-calc(20px)

  .private-tag
    cursor: inherit

  // Override default group nesting styles (only 5 layers deep are supported)
  .Select-option-group-label ~ .Select-option-group
    padding-left: 0

  .Select-option-group
    .Select-option, .Select-option-group .Select-option-group-label
      padding-left: rem-calc(20px)

    .Select-option-group
      .Select-option, .Select-option-group .Select-option-group-label
        padding-left: rem-calc(40px)

      .Select-option-group
        .Select-option, .Select-option-group .Select-option-group-label
          padding-left: rem-calc(60px)

        .Select-option-group
          .Select-option, .Select-option-group .Select-option-group-label
            padding-left: rem-calc(80px)

          .Select-option-group
            .Select-option, .Select-option-group .Select-option-group-label
              padding-left: rem-calc(100px)

.private-form__control--inline
  &.Select
    height: auto

  &.Select--multi
    .Select-arrow-zone
      display: none

    &.has-value
      .Select-multi-value-wrapper
        max-width: calc(100% - #{rem-calc(32px)})
        width: auto

  &:not(.Select--multi)
    .Select-input
      padding-left: 0

    & > .Select-control .Select-value
      padding-left: 0

  &.is-focused:not(.is-open) > .Select-control
    border-color: transparent
    box-shadow: none

  &.has-value
    .Select-arrow-zone
      display: table-cell

  &.is-open
    .Select-arrow-zone
      display: table-cell

    .Select-placeholder
      display: block

  .Select-control
    background: transparent
    border-color: transparent
    border-radius: 0

    &:hover
      background: transparent
      border-bottom-color: $color-calypso
      border-radius: 0

  .Select-multi-value-wrapper
    margin-left: rem-calc(-5px)

    .Select-input
      // When there are no entires, the margin is 10 instead of 5
      // Always keep it 5 in this case
      margin-left: rem-calc(5px)

  .Select-placeholder
    +set-font-size($input-sm-font-size)
    +set-placeholder-style
    padding-left: 0

  &:not(.private-form__control--inline--show-placeholder)
    .Select-placeholder
      display: none

// Multi-select values, rendered inside of a dropdown button
.private-form__selectplus--multi-values
  +display(flex)
  +flex-wrap(wrap)

  &.has-value-tags
    margin-top: rem-calc(-9px)  // hack to keep height of dropdown button consistent

  .private-tag
    margin-top: rem-calc(7px)

    &:not(:last-child)
      margin-right: rem-calc(5px)

    & + .private-tag
      margin-left: 0

    .private-icon
      line-height: normal  // the usual `line-height: 0` rule for icons in buttons breaks the vertical alignment

// Styles that apply to multi-select values under both anchor="button" and anchor="combined"
.private-form__selectplus--multi-values, .Select-multi-value-wrapper
  .private-dropdown__item__decoration
    +m-right-1
    height: rem-calc(16px)
    left: 0
    width: rem-calc(16px)

// Don't show padding around search results when there's a visible searchbox, or when virtualized
.private-search-control__wrapper:not(.sr-only) + .private-typeahead-results,
.private-dropdown--virtualized
  .Select-menu
    max-height: rem-calc($dropdown-menu-height)
    padding-bottom: 0
    padding-top: 0

// "Load more"
.private-form__selectplus--load-more-count
  color: $color-flint
  margin-right: rem-calc(4px)

// Smaller version
.private-form__selectplus--small
  &.private-form__selectplus--small
    +small-input-appearance
