@import HubStyle/stylesheets/hubstyle/base


/// Paginator styles

.private-paginator
  +align-items(center)
  +display(flex)
  +flex-flow(row nowrap)
  +justify-content(center)

.private-paginator__numbers
  +display(flex)
  +justify-content(center)

  @media only screen and (max-width: rem-calc(480px))
    display: none

.private-paginator__backward-controls,
.private-paginator__forward-controls
  .private-paginator__button
    margin-left: 0

.private-paginator__icon
  +set-font-size(24px)
  color: currentColor !important
  vertical-align: middle

.private-paginator__button
  +set-font-size($pagination-button-label-font-size)
  +ui-transition
  color: $base-link-color
  padding-left: rem-calc(7px)
  padding-right: rem-calc(7px)

  &:hover
    background-color: $pagination-hover-background-color
    text-decoration: none

  &:active
    background-color: $pagination-active-background-color
    color: $base-link-color

.private-paginator__button--numeric
  +set-font-size($pagination-button-font-size)

.private-paginator__button--active
  background-color: $pagination-selected-background-color
  border: 1px solid $pagination-selected-border-color
