// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

/// Textareas
/// and their variants like copyable, code, auto-grow

.private-text-area
  height: auto
  min-height: rem-calc($input-default-height)

.private-text-area--no-resize
  display: inline-block
  resize: none

.private-text-area--non-responsive
  width: auto

.private-text-area--vertical-resize
  resize: vertical

.private-text-area--horizontal-resize
  resize: horizontal

.private-form__control.private-form__control
  &.private-text-area--code,
  &.private-text-area--code[readonly]
    border-color: $form-control-border-color
    min-height: rem-calc($text-area-code-height)

    &.private-text-area--no-min-height
      min-height: 0

.private-text-area__counter-wrapper
  +display(flex)
  +flex-direction(column)

.private-text-area__counter,
.private-char-count__label,
.private-form__counter
  +align-self(flex-end)
  +set-font-size($base-font-size)
  color: $color-eerie
  margin-left: rem-calc(8px)
  padding: rem-calc($label-default-padding-top) 0 rem-calc($label-default-padding-bottom)

.private-char-count
  +display(flex)
  +flex-direction(column)

.private-char-count--negative
  .private-char-count__label
    color: $color-candy-apple

.private-text-area--copyable
  background-color: $form-copyable-background-color
