/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Toolbar styles
/// Includes dark theme, filter-bar, toolbar with tabs


.private-tool-bar
  padding: 0 rem-calc($layout-editor-padding)

.private-tool-bar--dark
  +display(flex)
  background-color: $toolbar-dark-background-color
  color: $toolbar-dark-foreground-color

  .private-tool-bar__inner
    min-height: rem-calc($toolbar-height-dark)

  .private-tool-bar__group
    // add minimum padding to separate items if they get stacked
    padding: rem-calc(($toolbar-height-dark - $input-default-height) / 2) 0

    &:nth-child(2)
      +flex-basis(auto)

  // Special styling for page titles on a UIEditorPage
  h1
    +font-smoothing(auto)
    +set-font-size($heading-4)
    +m-bottom-0
    font-weight: $web-font-regular-weight
    max-width: 100%
    white-space: normal


.private-tool-bar__inner
  +align-items(center)
  +display(flex)
  +flex-wrap(wrap)
  +justify-content(space-between)
  width: 100%

  > * + *
    margin-left: rem-calc($base-spacing-x)

.private-tool-bar__group

  > .private-button
    +flex-shrink(0)
    margin-top: 0
    width: auto

  // this is the middle item
  &:nth-child(2)
    +flex-shrink(1)
    +flex-grow(1)
    +justify-content(center)

    &:last-child
      +flex-grow(2)

  &:nth-child(3)
    &:last-child
      +justify-content(flex-end)

  // workaround for IE11 bug where items won't wrap
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
    +flex-basis(auto)

  // resets ^ for Edge
  @supports (-ms-ime-align: auto)
    +flex-basis(0%)

/// Modifiers

/// Filter bars generally have a search field as the last item
.private-tool-bar--with-filters
  margin: rem-calc($layout-editor-padding) 0
  padding-left: 0
  padding-right: 0

  .private-tool-bar__group
    +flex-basis(auto)
    +flex-grow(9999)

    &:last-child
      // setting a comfortable width for search
      +flex-basis(400px)
      +flex-grow(1)
      +justify-content(flex-end)
      margin-left: 0

// Specialized opinions about a toolbar containing tabs
.private-tool-bar--with-tabs
  border-bottom: 1px solid $color-battleship

  .private-tool-bar__inner
    +flex-wrap(nowrap)

  .private-tool-bar__group:not(:nth-child(2))
    +flex-grow(0)
    +flex-shrink(0)

  // the 2nd group should *always* be the tabs container. no reason to use this component otherwise
  .private-tool-bar__group:nth-child(2)
    +flex-basis(auto)
    max-width: 100%
    min-width: rem-calc(50px)   // this is a hedge against the tabs having so little space, they are hidden

.private-template__section--header
  .private-tool-bar--with-tabs
    border-bottom: 0

// sass-lint:disable no-mergeable-selectors
.private-tool-bar--with-tabs
  @media only screen and (max-width: rem-calc(map-get($grid-breakpoints, sm)))
    margin-top: rem-calc($layout-editor-padding)

    .private-tool-bar__inner
      +flex-wrap(wrap)

    .private-tool-bar__group
      &.private-tool-bar__group
        +flex-grow(1)
        margin-left: rem-calc($base-spacing-x)
        margin-right: rem-calc($base-spacing-x)
        margin-top: rem-calc($base-spacing-y)

      /// to avoid awkward gap, set tab component to be the last visual item when at `sm` breakpoint
      &:nth-child(2)
        +order(3)

      > .private-button
        +flex(1 0 auto)
// sass-lint:enable no-mergeable-selectors
