/* @bend-expect trellis-tokenized */

@import 'cssUtils/stylesheets/mixins/display';
@import 'cssUtils/stylesheets/mixins/flex-box';
@import 'cssUtils/stylesheets/utility-classes/utility-mixins/flex-grid-breakpoints';
@import 'cssUtils/stylesheets/utility-classes/utility-mixins/flex-grid-framework';
@import 'cssUtils/stylesheets/utility-classes/utility-mixins/flex-grid';

$enable-flex: true;
$enable-grid-classes: true;

$grid-breakpoints: (
  // Extra small screen / phone
  xs: 0,
  // Small screen / phone
  sm: 544px,
  // Medium screen / tablet
  md: 768px,
  // Large screen / desktop
  lg: 992px,
  // Extra large screen / wide desktop
  xl: 1200px
);

/// Grid system
//
// Generate semantic grid columns with these mixins.

$grid-columns: 12;
$grid-gutter-width: 32px;

// Container widths
//
// Set the container width, and override it for fixed navbars in media queries.
$container-max-widths: (
  sm: 576px,
  md: 720px,
  lg: 940px,
  xl: 1140px,
);

.container {
  @include make-container();
  @include make-container-max-widths();
}

// Fluid container
//
// Utilizes the mixin meant for fixed width containers, but without any defined
// width for fluid, full width layouts.

.container-fluid {
  @include make-container();
}

// Row
//
// Rows contain and clear the floats of your columns.

@if $enable-grid-classes {
  .row {
    @include make-row();
  }
}

// Columns
//
// Common styles for small and large grid columns

@if $enable-grid-classes {
  @include make-grid-columns();
}

// Flex variation
//
// Custom styles for additional flex alignment options.

@if $enable-flex and $enable-grid-classes {
  // Flex column reordering

  @each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
      .col-#{$breakpoint}-first {
        @include order(-1);
      }
      .col-#{$breakpoint}-last {
        @include order(1);
      }
    }
  }

  // Alignment for every column in row

  @each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
      .row-#{$breakpoint}-top {
        @include align-items(flex-start);
      }
      .row-#{$breakpoint}-center {
        @include align-items(center);
      }
      .row-#{$breakpoint}-bottom {
        @include align-items(flex-end);
      }
    }
  }

  // Alignment per column

  @each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
      .col-#{$breakpoint}-top {
        @include align-self(flex-start);
      }
      .col-#{$breakpoint}-center {
        @include align-self(center);
      }
      .col-#{$breakpoint}-bottom {
        @include align-self(flex-end);
      }
    }
  }
}
