/* @bend-expect trellis-tokenized */

// Need for IE, Edge, Firefox, and Safari
=user-select($select)
  -webkit-user-select: $select
  -moz-user-select: $select
  -ms-user-select: $select
  @if ($select == 'contain')
    // Tuck an error until it's available
    @error "Only edge supports contain https://www.chromestatus.com/feature/5730263904550912"
    -ms-user-select: element
  user-select: $select
