/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// UIDropdown anchor

.private-button__dropdown-opener

  .private-dropdown__item__decoration
    margin-right: rem-calc(4px)

  .private-dropdown__item__image
    margin-left: rem-calc(4px)

  &.private-form__control--inline--show-placeholder
    .private-dropdown__item__label
      +set-placeholder-style

.private-button__dropdown-opener.private-button__dropdown-opener--multiline
  height: auto

/// UIDropdown menu

.private-dropdown
  border-radius: inherit

.private-dropdown--list
  padding-bottom: rem-calc($dropdown-padding-y)
  padding-top: rem-calc($dropdown-padding-y)

  .private-list__item > a,
  .private-list__item > button,
  .private-list__item > .private-file-button .private-button
    +dropdown-item-appearance
    background-color: $color-olaf
    line-height: $base-line-height  // Fixes #1654

    .hubspot-disable-focus-styles &:hover,
    .hubspot-enable-focus-styles &:focus    // Scope "selected" states to avoid clashing
      +dropdown-item-hover
      text-decoration: none

    &.selected
      background-color: $dropdown-selected-background-color

  // Override button shaded background when disabled={true} for all `use`s (#8213)
  .private-button
    &.private-button--disabled
      background-color: transparent !important

  // Override button rule that forces centering when truncate={true} (#6066)
  .private-button .private-truncated-string--is-flex
    justify-content: initial

  .private-checkbox
    min-height: $dropdown-item-default-height
    padding-left: rem-calc(24px)
    padding-right: rem-calc($dropdown-padding-x)

    .private-checkbox__text
      padding-left: rem-calc(23px)

    &:hover
      +dropdown-item-hover

  &.private-searchable-select-input__dropdown
    padding-top: 0

.private-dropdown__caret
  +make-triangle($size: 5px)
  align-self: center // needed for IE11
  flex-shrink: 0

.private-button__link .private-dropdown__caret,
.private-button--transparent .private-dropdown__caret
  color: $icon-default-color
  margin-left: rem-calc(2px, em)

  &.private-dropdown__caret--link-on-dark
    color: inherit

.private-button--sm .private-dropdown__caret
  border-width: 4px // em units make the caret slightly too small at 12px font-size
  margin-top: 4px // em units cause rounding glitch in Chrome (#8132)

.private-dropdown__caret--enclosed
  margin-left: 4px

.private-dropdown__caret--form
  +make-triangle($color: $dropdown-caret-color, $size: 6px)

.private-dropdown__caret--hidden
  visibility: hidden

.private-dropdown__icon
  +m-right-2
  line-height: 1

.private-dropdown__footer-container
  +p-y-4
  +p-x-5
  border-top: 1px solid $dropdown-border-color

.private-dropdown__loading
  > .private-typeahead-result-label.private-typeahead-result-label.private-typeahead-result-label
    background-color: $color-olaf !important // extra specificity to override disabled button style
    padding-bottom: rem-calc(4px)
    padding-top: rem-calc(4px)

  .private-spinner
    margin-left: auto
    margin-right: auto

// UITypeahead

.private-typeahead
  &.hide-search
    > .private-search-control__wrapper
      +sr-only

    > .private-typeahead-results
      max-height: rem-calc($dropdown-menu-height + $dropdown-padding-y)
      padding-bottom: rem-calc($dropdown-padding-y)
      padding-top: rem-calc($dropdown-padding-y)

  &.layout-grouped
    >.private-typeahead-results
      +display(flex)
      overflow-y: hidden

      >.private-typeahead-results-group
        +flex-basis(0)
        +flex-grow(1)
        max-height: rem-calc($dropdown-menu-height)
        overflow-x: hidden
        overflow-y: auto

.private-typeahead-results-group__list
  margin-bottom: 0

.private-search-control__wrapper
  background-color: $dropdown-search-wrapper-background-color
  border-top-left-radius: inherit
  border-top-right-radius: inherit
  padding: rem-calc(12px)
  position: relative

  .private-search-control__input
    background-color: $dropdown-search-background-color
    padding-right: 30px

.private-search-control__wrapper--toolbar
  padding: rem-calc(8px)

.private-dropdown__button__contents
  align-items: center
  display: flex

  .private-button__link
    display: inline-flex

// sass-lint:disable property-sort-order
.private-dropdown__button-label
  +flex-grow(1)
  +m-right-1
  +truncate-text
  min-width: 0
  text-align: left

  // prevent descenders from being clipped (#1068)
  padding-bottom: 2px
  padding-top: 2px
  margin-bottom: -2px
  margin-top: -2px

// sass-lint:enable property-sort-order

.private-form__control--inline

  .private-dropdown__button-label
    +flex-grow(0)
    +m-right-2
    width: auto

.private-dropdown__button-label--no-icon
  +m-right-0

.private-button__link
  .private-dropdown__button__contents
    display: inline-flex // needed for IE11
    text-decoration: inherit // needed for Firefox

  .private-dropdown__button-label
    white-space: normal

.private-dropdown__item__decoration
  +flex-shrink(0)
  margin-right: rem-calc(8px)
  width: rem-calc(16px)

.private-dropdown__item__image
  border-radius: 100%
  display: inline-block
  height: rem-calc(24px)
  left: rem-calc(-4px)
  position: relative
  top: rem-calc(-2px)  // vertical alignment tweak
  vertical-align: middle
  width: rem-calc(24px)

.private-typeahead-results
  -webkit-tap-highlight-color: rgba($color-monolith, 0)
  border-radius: inherit
  list-style: none
  margin: 0
  max-height: rem-calc($dropdown-menu-height)
  overflow-x: hidden
  overflow-y: auto
  padding: 0
  position: relative

  .private-typeahead-result-label
    padding: rem-calc(8px) rem-calc(20px)

  .private-typeahead-result-heading
    cursor: auto

  .private-typeahead-result--disabled
    .private-typeahead-result-label
      +set-disabled-style
      background-color: $color-olaf !important

  .private-typeahead-result--highlighted
    +dropdown-item-hover

  .private-dropdown__item--toggleable
    +align-items(center)
    +display(flex)
    +set-font-size(15px)  // override checkbox font-size
    padding-right: rem-calc(16px)
    white-space: nowrap

    .private-dropdown__item__decoration
      margin-left: rem-calc(8px)  // ensure space between checkbox and decoration

  .private-dropdown__item__label
    overflow: hidden
    text-overflow: ellipsis

.private-typeahead-results--toolbar.private-typeahead-results--toolbar
  .private-typeahead-result-label
    +set-font-size(13px)
    min-height: auto
    padding: rem-calc(3px) rem-calc(6px)

.private-dropdown__badge
  margin-left: rem-calc(4px)
  margin-top: rem-calc(-2px)  // hack for vertical centering
  vertical-align: middle

.private-typeahead-results-item__help
  +user-select(none)
  cursor: pointer
  display: block
  margin-top: rem-calc(-3px)
  padding: 0 rem-calc(20px) rem-calc(9px)
  white-space: normal
  word-wrap: break-word

  .private-tag
    margin-bottom: rem-calc(-8px)  // Hack to make padding match that of normal help text

  .private-tag--unenclosed__text
    font-weight: $web-font-regular-weight

.private-checkbox + .private-typeahead-results-item__help
  margin-left: rem-calc(34px) // Align with checkbox text
  margin-top: 0
  padding: 0 rem-calc(20px) rem-calc(8px)  // For multi-select case

.private-typeahead-results-group__label
  +dropdown-group-label

.private-typeahead-results-group__label ~ .private-typeahead-results-group
  padding-left: 0

// indent the options underneath UITypeahead groups, but not UIGroupedTypeahead groups
.private-typeahead:not(.layout-grouped)
  .private-typeahead-results-group .private-typeahead-result,
  .private-typeahead-results-group .private-typeahead-results-group .private-typeahead-results-group__label
    padding-left: rem-calc(20px)

// prevent helptext from wrapping when virtualized
.private-typeahead-option--virtualized
  .private-typeahead-results-item__help
    white-space: nowrap

.private-typeahead-result__loading-indicator
  margin: 0
  margin-left: rem-calc(4px)
  transform: scale(.75)
