@import HubStyle/stylesheets/hubstyle/base

.private-help-icon__icon
  cursor: default

.private-help-icon--inline
  .private-help-icon__icon
    margin-left: rem-calc(8px)

.private-table thead th
  .private-help-icon--inline
    .private-help-icon__icon
      margin-left: rem-calc(4px)

// Inside UIFormControl label
.private-form__help-tooltip
  padding-top: 8px

  .private-help-icon__icon
    vertical-align: $input-vertical-alignment

    @-moz-document url-prefix()
      vertical-align: bottom
