@import HubStyle/stylesheets/hubstyle/base

/// Close button
/// Exceptions where the container sets a modification. These aren't ideal, but
/// here we are. Perhaps we can use context on these.

.private-modal--default
  .private-modal__control
    &.private-modal__close, &:not(.private-close__button)
      top: rem-calc(($modal-header-height / 2) - ($modal-close-button-size / 2))

.private-modal__header--with-toolbar
  .private-modal__control
    &.private-modal__close, &:not(.private-close__button)
      margin-left: rem-calc(24px)
      position: relative
      right: 0
      top: rem-calc(22px)

