////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

/// Modifier classes; when Components are overkill.
/// These are visually-opinionated modifiers that are specific to
/// the Space-Sword theme and don't belong in cssUtils.

/// Use the following prefixes:
/// `.is--*` for applying directly to elements
/// `.has--*` for applying to parent containers to affect children

/// Modifiers should generally be immutable so !important is okay.


/// Heading modifiers (e.g. `.is--heading-2`)

$vertical-separation-lg: 40px

@each $size, $attrs in $heading-sizes
  .is--#{$size}
    +make-heading(nth($attrs, 1), nth($attrs, 3), nth($attrs, 4), $important: true)

/// @todo include subset of cssUtils so we can document them


/// allows for multiline table cells
.is--multiline__cell
  overflow: visible
  white-space: normal !important

.is--single-line
  white-space: nowrap

/// Module spacing

/// Making use of the lobotomized-owl ( * + * ) selector for predictable
/// spacing within modules. Spacing should try to go in a single direction
/// only for predictability; to the bottom and to the left.

/// Vertical spacing

.has--vertical-spacing
  > :last-child
    margin-bottom: 0

/// Horizontal spacing

.has--horizontal-spacing
  > * + *
    margin-left: rem-calc($base-spacing-x)

.has--horizontal-spacing--md
  > * + *
    margin-left: rem-calc($base-spacing-x * 2) !important

// Module vertical spacing

.is--module
  +single-direction-module-spacing

.is--island.is--island
  +single-direction-module-spacing($vertical-separation-lg)
  margin-top: rem-calc($vertical-separation-lg)


// To hide the grippie by default and show on hover, the parent
// container needs the `.has--draggable` modifier class

.has--draggable
  .is--draggable
    background-size: 0 0

  .is--draggable--sized
    &::before
      opacity: 0

  &:hover
    .is--draggable
      background-size: auto

    .is--draggable--sized
      &::before
        opacity: 1

    .is--draggable-hover-container
      .is--draggable--standalone
        opacity: 0

      &:hover
        .is--draggable--standalone
          opacity: 1

// text variations

.is--text--regular
  font-family: $web-font-family !important
  font-weight: $web-font-regular-weight !important

.is--text--medium
  font-family: $web-font-family !important
  font-weight: $web-font-medium-weight !important

.is--text--demibold
  font-family: $web-font-family !important
  font-weight: $web-font-demi-bold-weight !important

.is--text--bold
  font-family: $web-font-family !important
  font-weight: $web-font-demi-bold-weight !important

.is--text--error
  color: $color-candy-apple !important

.is--text--help
  color: $color-slinky !important

.is--text--success
  color: $color-oz-dark !important

.is--text--disabled
  color: $color-button-disabled-text

/// Container modifier to set foreground color to white
.has--on-dark
  color: $base-font-contrast-color

.is--medium-contrast
  background-color: $base-background-color-medium

.is--high-contrast
  background-color: $base-background-color-dark
  color: $base-font-contrast-color

/// indicate selection of a currently active item, e.g. table rows
.is--selected-item
  background-color: $base-selection-background-color
