/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

/// Containers for form field and label pairs
/// Options for inline forms

$form-control-selected-background-color: $color-calypso-light

@import HubStyle/stylesheets/hubstyle/base

.private-form__set
  // automatic spacing between each form__set
  + .private-form__set
    margin-top: rem-calc($form-set-vertical-separation)

  + .private-form-actions
    margin-top: rem-calc(44px)

  &.private-form__set--no-label + .private-form-actions
    margin-top: rem-calc($form-set-vertical-separation)

  &.private-form__set--flush
    margin-top: 0 !important

  &.private-form__set--separated
    margin-top: rem-calc($form-set-vertical-separation) !important

.private-form__label-wrapper
  +display(flex)
  +flex-wrap(wrap)
  +justify-content(space-between)
  flex-direction: column

  .private-form__label--with-tooltip
    width: auto

  .private-form__label
    +flex(1 0 auto)
    max-width: 100%
    // future candidate for styled-components to determine padding-bottom based on if there
    // is `help` rather than overriding and moving padding to .private-form__label-wrapper
    // ---
    // necessary to keep `UIFormLabel` correct on it's own
    padding-bottom: 0

.private-form__meta
  +display(flex)
  +flex-wrap(nowrap)
  +justify-content(space-between)

  .private-form__counter
    +align-self(flex-start)
    +flex-shrink(0)
    +set-font-size($microcopy-size)
    font-weight: $web-font-medium-weight
    padding-top: $form-set-validation-top-spacing

.private-form__messages
  +display(flex)
  +flex-direction(column)

.private-form__set--floating
  padding-bottom: rem-calc(5px)
  padding-top: rem-calc(18px)
  position: relative

  &:hover
    .private-form__control__overlay
      opacity: 1
      pointer-events: inherit

    .private-form__control__overlay--hidden
      opacity: 0
      pointer-events: none

    .private-form__control--inline
      border-bottom-color: $color-calypso
      border-radius: 0

      &[readonly]:not([aria-expanded]),
      &.private-button--readonly
        border-color: transparent

  &:hover,
  &.private-form__set--floating--focused
    .private-button.private-form__control
      background-color: transparent

  + .private-form__set--floating
    +m-top-0

  .private-form__control__overlay
    align-items: center
    background: $color-olaf
    box-shadow: rem-calc(-8px) 0 rem-calc(6px) rem-calc(-4px) $color-olaf
    display: flex
    height: rem-calc(48px)
    opacity: 0
    padding-left: rem-calc(5px)
    pointer-events: none
    position: absolute
    right: 0
    top: 0
    z-index: $mercury-layer

  .private-form__control__overlay--visible
    opacity: 1
    pointer-events: inherit

  .private-form__control-floating__edit-icon
    +m-right-2
    cursor: pointer

  .private-form__validation-wrapper
    margin-top: rem-calc(1px)
    overflow: auto

    .private-form__validation
      float: right

.private-floating-form
  .private-form__set--floating
    border-bottom: 1px solid transparent
    border-top: 1px solid transparent

    + .private-form__set--floating
      margin-top: -1px
