@import HubStyle/stylesheets/hubstyle/base

/// Tooltips
/// Lifted directly from Bootstrap and modified to use BEM syntax

/// Base class
.private-tooltip
  display: block
  position: relative
  visibility: visible
  z-index: $mars-layer

  &:not(.private-tooltip--longform)
    a
      +link-style-on-dark

    .private-icon:not(.private-icon-circle__inner)
      +icon-within-link($dark-link-icon-color)

/// Subcomponents

/// Wrapper for the tooltip content
.private-tooltip__inner
  box-shadow: $tooltip-shadow
  line-height: 1.5
  max-width: 100%
  padding: rem-calc($tooltip-padding-y) rem-calc($tooltip-padding-x)
  text-decoration: none
  word-wrap: break-word

.private-tooltip__heading
  margin-bottom: 8px

/// Modifiers

.private-tooltip--longform
  +set-font-size($tooltip-longform-font-size)
  max-width: 280px

  .private-tooltip__inner
    box-shadow: none
    color: inherit
    max-width: inherit
    padding: rem-calc($tooltip-longform-padding)

  ul, ol
    +p-left-4
