/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

// UISelectableBox

.private-selectable-box
  +button-borders
  +user-select(none)
  background-color: $color-olaf
  border-color: $selectable-box-border-color
  cursor: pointer
  display: inline-block
  position: relative
  width: auto

  &.private-selectable-box--padded
    padding: rem-calc(20px)

  &.private-selectable-box--hovered
    box-shadow: 0 0 0 1px $selectable-box-border-color, 0 0 12px 0 $selectable-box-shadow-color

  &.private-button--active,
  &.private-selectable-box--selected
    background-color: $selectable-box-selected-background-color

  &.private-selectable-box--selected
    border-color: $selectable-box-selected-border-color
    box-shadow: 0 0 0 1px $selectable-box-selected-border-color

    &.private-selectable-box--hovered
      box-shadow: 0 0 0 1px $selectable-box-selected-border-color, 0 0 12px 0 $selectable-box-shadow-color

  &.private-selectable-box--readonly
    +set-readonly-style

  .private-selectable-box__selection-mark
    position: absolute

    .private-form__checkbox
      outline: 0

    .private-checkbox__input[readonly]
      + .private-checkbox__indicator
        border-color: $form-toggle-border-color !important

        .private-radio__circle
          stroke: $form-toggle-border-color

      &:focus + .private-checkbox__indicator
        outline: auto !important

    &.private-selectable-box__selection-mark--top-right
      right: rem-calc(12px)
      top: rem-calc(16px)

    &.private-selectable-box__selection-mark--left
      left: rem-calc(20px)
      margin-top: rem-calc($toggle-button-size / -2)
      top: 50%

      + .private-selectable-box__inner
        padding-left: rem-calc($toggle-button-size + 2px)

  .private-selectable-box__inner
    +font-smoothing
    +set-font-size($input-font-size)
    margin-left: 0
    white-space: normal

    .private-icon
      line-height: normal

  &.private-button--block
    width: 100%

  &.private-selectable-box--flush
    padding: 0 !important

  img
    transform: translateZ(0) // hack for incredibly weird Chrome bug (#5508)


// UISelectableButton

.private-selectable-button
  +display(flex)
  max-width: rem-calc($selectable-button-max-width)
  text-align: center
  width: 100%

  .private-selectable-button__inner
    +align-items(center)
    +display(flex)
    +justify-content(center)
    width: 100%

    .private-selectable-button__label
      margin-left: rem-calc(20px + $base-icon-spacing-x)
      margin-right: rem-calc(16px + $base-icon-spacing-x)
      width: 100%

    .private-icon
      line-height: normal

    img

      + .private-selectable-button__label
        margin-bottom: rem-calc(8px)

  // for buttons that will resize with their content
  &.private-selectable-button--auto
    display: inline-block
    max-width: none
    width: auto

    .private-selectable-button__inner
      gap: rem-calc(16px)
      padding: rem-calc(16px)

    &.private-button--block
      width: 100%

  // for the large, box-y buttons
  &.private-selectable-button--large
    .private-selectable-button__inner
      +flex-direction(column)
      +justify-content(center)
      min-height: rem-calc($selectable-button-large-min-height)
      padding: rem-calc(12px)

      .private-selectable-button__label
        padding-top: rem-calc(16px)

      .private-icon
        width: 100%

  // for the small, rectangular buttons
  &.private-selectable-button--small
    .private-selectable-button__inner
      gap: rem-calc(16px)
      min-height: rem-calc($selectable-button-small-height)
      padding: rem-calc(12px)

    .private-selectable-button__label--truncatable
      overflow: hidden
      text-overflow: ellipsis
      white-space: nowrap

    .private-selectable-button__checkbox
      padding-left: rem-calc(16px)

    .private-selectable-button__label--no-selection-mark
      margin-left: 0
      margin-right: 0

    .private-selectable-button__label--selection-mark
      padding-left: rem-calc(30px)

    .private-icon
      padding-left: rem-calc(4px)

    img
      margin-left: rem-calc(4px)

      + .private-selectable-button__label
        margin: 0 rem-calc(16px) 0 rem-calc(8px)

.private-button--block
  max-width: none

.private-selectable-button__label
  +font-smoothing
  +set-font-size($input-font-size)
  display: block
  white-space: normal
  width: 100%
