////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base

/// Lists
/// Resetting default margin on unstyled lists

.private-list--unstyled
  list-style: none
  margin-bottom: 0
  padding-left: 0

.private-list--inline
  display: inline-block
  max-width: 100%

  > li
    +p-left-1
    +p-right-1
    display: inline-block
    max-width: 100%

/// Inline-divided list items
/// * uses uneven lateral padding to account for width of divider

.private-list--inline-divided
  &.private-list--inline
    li:first-child
      padding-left: 0

    li:last-child
      padding-right: 0

    > li
      padding-right: rem-calc($inline-divided-list-spacing-x - 1)

      + li
        +background-image-linear-gradient(to bottom, $color-battleship, $color-battleship)
        background-position: 0 50%
        background-repeat: no-repeat
        background-size: 1px 60%
        padding-left: rem-calc($inline-divided-list-spacing-x)
