/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

/// Containers for form field and label pairs (in legacy mode)

@import HubStyle/stylesheets/hubstyle/base

// old-style description that splits layout
.private-form__set--legacy
  &.private-form__set--with-details
    +display(flex)

    .private-form__control-wrapper
      +flex-basis(60%)

  .private-form__desc
    +flex-basis(40%)
    +display(flex)
    margin: rem-calc(36px) auto 0

    .private-microcopy
      +m-left-5

  // restore UIHelpIcon styling
  .private-form__help-tooltip
    +set-font-size($base-font-size)
    color: $color-battleship
    cursor: pointer
    margin-left: rem-calc($base-icon-spacing-x, em)
    padding: rem-calc($label-default-padding-top) 0 rem-calc($label-default-padding-bottom)

  // spacing / alignment of help text and tooltip
  .private-form__inlinehelp
    +m-left-0
    +set-font-size($base-font-size)
    font-weight: $web-font-regular-weight
    padding: rem-calc($label-default-padding-top) 0 rem-calc($label-default-padding-bottom)

  .private-form__inlinehelp,
  .private-form__help-tooltip
    float: right

.private-form__set--no-label .private-form__desc
  +m-top-0
