/* @bend-expect trellis-tokenized */

////
/// @group elements
/// @author HubSpot/faast
////


@import HubStyle/stylesheets/hubstyle/base
@import cssUtils/stylesheets/utility-classes/utility-mixins/truncate-text

/// Typography

/// Anti-aliasing is explicitly not used per this article:
/// @link http://usabilitypost.com/2012/11/05/stop-fixing-font-smoothing/

/// `text-rendering: optimizeLegibility` is explicitly not on by default per
/// these articles (exception: light text on dark backgrounds):
/// @link https://css-tricks.com/almanac/properties/t/text-rendering/
/// @link https://bocoup.com/weblog/text-rendering

body
  +set-font-family
  +set-font-size($trellis-typography-body-100-font-size)
  color: $trellis-color-text-core-default
  line-height: rem-calc($trellis-typography-body-100-line-height)


/// build default rules for H1-H6 headers

@each $size, $attrs in $heading-sizes
  @if nth($attrs, 2)
    #{nth($attrs, 2)}
      +make-heading(nth($attrs, 1), nth($attrs, 3), nth($attrs, 4))

.private-page__title h1
  +make-heading($trellis-typography-heading-300-font-size, $trellis-typography-heading-300-font-weight)
  margin: $trellis-space-0

/// Code styles

.private-pre--code,
.private-code,
.is--code,
code
  color: $trellis-color-text-core-default
  font-family: $trellis-typography-code-100-font-family
  font-weight: $trellis-typography-code-100-font-weight

/// Inline code

code
  +font-smoothing(auto)
  +set-border-radius
  background-color: $trellis-color-fill-accent-neutral-subtle-alt
  padding: rem-calc($trellis-space-50) rem-calc($trellis-space-100)

/// Code block

// sass-lint:disable no-qualifying-elements
.private-pre--code,
pre.is--code
  background-color: $trellis-color-fill-surface-raised
  border: $trellis-border-width-0
// sass-lint:enable no-qualifying-elements

/// Microcopy/helper text
small,
.private-microcopy
  +set-font-size($trellis-typography-detail-200-font-size)
  line-height: rem-calc($trellis-typography-detail-200-line-height)

big,
.private-big
  +set-font-size($trellis-typography-body-200-font-size)

// UIBreakString
.private-break-string
  -ms-word-break: break-all  // needed for IE11
  display: block
  word-break: break-word     // needed for Chrome for Windows
  word-wrap: break-word

.private-break-string--hyphenate
  +hyphenate-words

.private-break-string--no-hyphenate
  +hyphenate-words(none, true)

// Special case to break strings more aggressively in tables in Edge. See:
// https://git.hubteam.com/HubSpot/UIComponents/pull/2133#issuecomment-395880
.private-table .private-break-string
  @supports (-ms-ime-align: auto)
    word-break: break-all    // needed for Edge 16
