// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Form groups
/// a.k.a., add-ons, compound forms, etc.
/// @todo add responsive behavior with container queries

.private-form__group--start,
.private-form__group--start > *
  border-bottom-right-radius: 0 !important
  border-top-right-radius: 0 !important

.private-form__group--end,
.private-form__group--end > *
  border-bottom-left-radius: 0 !important
  border-left-width: 0 !important
  border-top-left-radius: 0 !important

.private-form__group--start,
.private-form__group--end
  &:focus
    position: relative
    z-index: $mercury-layer

.private-form__group--start > .private-form__control,
.private-form__group--end > .private-form__control
  &:focus
    position: relative
    z-index: $venus-layer
