/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

@import HubStyle/stylesheets/hubstyle/base
@import cssUtils/stylesheets/utility-classes/utility-mixins/truncate-text

// UITruncateString

.private-truncated-string
  display: table
  min-width: 0
  table-layout: fixed
  width: 100%

  &.private-truncated-string--is-flex
    align-items: baseline
    display: flex

    .private-truncated-string__inner
      display: block
      min-width: 0
      width: auto

  &.private-truncated-string--has-tooltip
    cursor: help

    &:focus
      outline: 1px dotted

    a &, button &
      cursor: pointer

  .private-truncated-string__inner
    +truncate-text
    display: table-cell
    vertical-align: middle

.private-truncated-string__reverse__tail
  overflow: hidden

  .private-truncated-string__reverse__content
    display: block

  &.is-truncated
    float: right
    width: calc(100% - 1em)

    .private-truncated-string__reverse__content
      float: right

.private-truncated-string__reverse__ellipse
  +user-select(none)
  float: left

// In a width-constrained UIHeader title, prevent truncation
.private-header--width-constrained
  .private-header__title
    .private-truncated-string__inner
      white-space: normal

// In a required UIFloatingFormControl, show the asterix
.private-form__label--floating.private-form__label--required
  .private-truncated-string
    &::after
      // Non-breaking space
      content: '\00a0 *'
