/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base

/// Microlib for Media object

.private-media
  +display(flex)
  +flex-direction(row)

  &.private-media--align-start
    +align-items(flex-start)

  &.private-media--align-baseline
    +align-items(baseline)

  &.private-media--align-middle
    +align-items(center)

  &.private-media--align-bottom
    +align-items(flex-end)

  &.private-media--align-stretch
    +align-items(stretch)

  > * + *
    margin-left: rem-calc($base-spacing-x)

  /// @todo convert to tokens w/ passing build
  &.private-media--spaced-xs
    > * + *
      margin-left: rem-calc($distance-measurement-extra-small)

  &.private-media--spaced-md
    > * + *
      margin-left: rem-calc(16px)

  &.private-media--spaced-lg
    > * + *
      margin-left: rem-calc(20px)

  &.private-media--spaced-xl
    > * + *
      margin-left: rem-calc($distance-measurement-medium)

  &.private-media--collapsed
    > * + *
      margin-left: 0

.private-media__body
  +flex(1)
  +order(2)
  min-width: 0  /// added to let media objects render ellipsis

.private-media__item--left
  +order(1)

.private-media__item--right
  +order(3)
